import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {Seccion} from '../../models/seccion';
import {RestauranteService} from '../../../services/restaurante.service';
import {UsuarioService} from '../../../services/usuario.service';
import {GLOBAL} from '../../../services/global';
import {Location} from '@angular/common';

@Component({
    selector: 'seccion',
    templateUrl: './seccion.component.html',
    providers: [RestauranteService, UsuarioService]
}) 

export class SeccionComponent implements OnInit{				
    public identityR;
    public token;
    public title: string;
    public title2: string;
    public url: string;
    public seccion: Seccion;
    public status: string;
    public id: any;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _restauranteService: RestauranteService,
        private _location: Location
    ){
        this.title ='Secciones o clasificacin de tus productos';
        this.identityR = this._usuarioService.getIdentityR();
        this.token = this._usuarioService.getToken();
        this.url = GLOBAL.url;
        this.status = null;
        
    }

    ngOnInit(){
        this.loadPage();
    }

    backClicked() {
        // this._router.navigate(['/restaurante/perfil']);
        this._location.back();
    }

    loadPage(){
        this._route.params.subscribe(params => {
            this.id = 0;
            this.title2 ='Crear seccion';

            if(params['id'] != 0){ 
                this.id = params['id'];
                this.title2 ='Actualizar seccin';
            }

            if(this.id != 0){
                this.obtenerSeccion(this.id);                
            } else {
                this.seccion = new Seccion("", "","",null);
            }
        })
    }

    obtenerSeccion(id){
        this._restauranteService.obtenerSeccion(id).subscribe(
            response => {                
                if(response.seccion && response.seccion._id){
                    this.seccion = response.seccion;
                } else {
                    this.status = 'error2';
                }
            },
            error => {
                this.status = 'error2';
                // console.log(<any>error)
            }
        )
    }

    onSubmit(form){
        if(this.id == 0){
            this.action1(form);
        } else {
            this.action2();
        }
    }

    action1(form){
        this._restauranteService.crearSeccion(this.token, this.identityR._id, this.seccion).subscribe(
            response => {
                if(response.seccion && response.seccion._id){
                    form.reset();
                    // this._router.navigate(['/restaurante/perfil']);
                    this._location.back();
                } else {
                    this.status = 'error1';
                }
            },
            error => {
                this.status = 'error1';
                // console.log(<any>error)
            }
        );
    }

    action2(){
        this._restauranteService.actualizarSeccion(this.token, this.identityR._id, this.seccion._id, this.seccion).subscribe(
            response => {
                if(response.seccion && response.seccion._id){
                    this.status = 'success';
                    this._location.back();
                } else {
                    this.status = 'error2';
                }
            },
            error => {
                this.status = 'error2';
                // console.log(<any>error)
            }
        );
    }
}



