import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {GLOBAL} from './global';


@Injectable()
export class CuponService{
    public url: string;

    constructor(private _http: HttpClient){
        this.url = GLOBAL.url;
    }
    

    nuevoCupon(codigo, token): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                                .set('Authorization', token);
        return this._http.get(this.url+'cupon/' + codigo, {headers: headers});
    }

    obtenerCupones(token, n): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                                .set('Authorization', token);
        return this._http.get(this.url+'obtener-cupones' + '/' + n, {headers: headers});
    }

    actualizarCupon(token, cupon): Observable<any>{
        let params = JSON.stringify(cupon);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                                .set('Authorization', token);
        return this._http.post(this.url+'actualizar-cupon', params, {headers: headers});
    }

    todosLosCupones(token): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                                .set('Authorization', token);
        return this._http.get(this.url+'todos-los-cupones', {headers: headers});
    }
}