import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {Restaurante} from '../../models/restaurante';
import {Seccion} from '../../models/seccion';
import {Producto} from '../../models/producto';
import {ProductoService} from '../../../services/producto.service';
import {RestauranteService} from '../../../services/restaurante.service';
import {UsuarioService} from '../../../services/usuario.service';
import {GLOBAL} from '../../../services/global';
import {Location} from '@angular/common';
import * as $ from 'jquery';


@Component({
    selector: 'PerfilRestaurantR',
    templateUrl: './PerfilRestaurantR.component.html',
    providers: [ProductoService, RestauranteService, UsuarioService]
}) 

export class PerfilRestaurantRComponent implements OnInit{				
    public title: string;
    public restaurante: Restaurante;
    public secciones: Seccion[];
    public productos: any;
    public producto: Producto;
    public status: string;
    public identity;
    public identityR;
    public token;
    public url: string;
    public peticion: any;
    public page: number;
    public pages: number;
    public loading: boolean;
    public productoStatus;
    public id: any;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _productoService: ProductoService,
        private _restauranteService: RestauranteService,
        private _usuarioService: UsuarioService,
        private _location: Location
    ){
        this.identityR = this._usuarioService.getIdentityR();
        this.restaurante = this.identityR;
        this.url = GLOBAL.url;
        this.title = this.identityR.nombre;
        this.productos = [];
        this.page = 1;
        this.loading = false;
        this.status = null;
        this.id = null;
    }


    ngOnInit(){
        this.obtenerRestaurante(this.identityR._id);
        this.obtenerSecciones(this.identityR._id, this.page);
        this.identity = this._usuarioService.getIdentity()
        this.token = this._usuarioService.getToken();
        this.checkModal();
    }

    checkModal(){
        if($('.modal.in, .modal.show').length > 0){
            window.location.reload();
        }
    }

    backClicked() {
        // this._router.navigate(['/restaurante/inicio']);
        this._location.back();
    }

    onScroll(){
        this.page += 1;
        
        if(this.page == this.pages+1){
            this.loading = false;
        } else {
            this.loading = true;
            this.obtenerSecciones(this.identityR._id, this.page, true);
        }
    }

    obtenerRestaurante(id){
        this._restauranteService.obtenerRestaurante(id).subscribe(
            response => {
                if(response.restaurante){
                    this.restaurante = response.restaurante;
                    // console.log(this.restaurante);
                }
            },
            error => {
                var errorMessage = <any> error;
                // console.log(errorMessage);
   
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    obtenerSecciones(id, page, adding = false){
        this._restauranteService.obtenerSecciones(id, page, 0).subscribe(
            response => {
                if(response.secciones){
                    this.pages = response.pages;

                    response.secciones.forEach(element => {
                        this.obtenerProductosRestaurante(element._id);
                    });

                    if(!adding){
                        this.secciones = response.secciones;
                        this.loading = false;
                    } else {
                        var arrayA = this.secciones;
                        var arrayB = response.secciones;
                        this.secciones = arrayA.concat(arrayB);
                        this.loading = false;
                    }
                }
            },
            error => {
                var errorMessage = <any> error;
                // console.log(errorMessage);
   
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    obtenerProductosRestaurante(seccionId){
        this._productoService.obtenerProductosRestaurante(seccionId).subscribe(
            response => {
                if(response.productos){
                    response.productos.forEach(element => {
                        this.productos.push(element);
                    });
                }
            },
            error => {
                var errorMessage = <any> error;
                // console.log(errorMessage);
   
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    // obtenerProductos(seccionId, page){
    //     this.peticion = '0/0/' + seccionId + '/0';
    //     this._productoService.obtenerProductos(this.peticion, page).subscribe(
    //         response => {
    //             if(response.productos){
    //                 response.productos.forEach(element => {
    //                     this.productos.push(element);
    //                 });
    //             }
    //         },
    //         error => {
    //             var errorMessage = <any> error;
    //             // console.log(errorMessage);
   
    //             if(errorMessage != null){
    //                 this.status = 'error';
    //             }
    //         }
    //     )
    // }

    preparar(id){
        this.id = id;
    }

    preparar2(status){
        this.productoStatus = status;
    }

    ejecutar(numeroDeLaFuncion){
        if(numeroDeLaFuncion == 0){
            this._router.navigate(['/restaurante/producto/', this.id])
        
        } else if(numeroDeLaFuncion == 2){
            this.eliminarProducto(this.id);
        
        } else if(numeroDeLaFuncion == 3){
            this.activarProducto(this.id, 1);
        
        } else if(numeroDeLaFuncion == 4){
            this.activarProducto(this.id, 2);

        } else if(numeroDeLaFuncion == 6){
            this.eliminarSeccion(this.id);      
        }
    }

    activarProducto(producto_id, status){
        this._productoService.activarProducto(this.token, producto_id, status).subscribe(
            response => {
                if(response){
                    // window.location.reload();
                    this.productos = [];
                    this.page = 1;
                    this.obtenerSecciones(this.identityR._id, this.page);
                    this.productoStatus == null;
                    this.id == null;
                }
            },
            error => {
                var errorMessage = <any> error;
                // console.log(errorMessage);
   
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    eliminarProducto(producto_id){
        this._productoService.eliminarProducto(this.token, producto_id).subscribe(
            response => {
                if(response){
                    // window.location.reload();
                    this.productos = [];
                    this.page = 1;
                    this.obtenerSecciones(this.identityR._id, this.page);
                    this.productoStatus == null;
                    this.id == null;
                }
            },
            error => {
                var errorMessage = <any> error;
                // console.log(errorMessage);
   
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    eliminarSeccion(id){
        var n = 0;
        var total = this.productos.length;
        var vacio = true;

        this.productos.forEach(element => {                        
            if(id == element.seccion){
                vacio = false;
                console.log(element);
            }

            n = n + 1;

            if(n == total){
                if(vacio == false){
                    return alert('No puedes eliminar una seccin que contenga productos');
                } else if(vacio == true){
                    this._restauranteService.eliminarSeccion(this.token, id).subscribe(
                        response => {
                            if(response){
                                this.productos = [];
                                this.page = 1;
                                this.obtenerSecciones(this.identityR._id, this.page);
                                this.productoStatus == null;
                                this.id == null;
                            }
                        },
                        error => {
                            var errorMessage = <any>error;
                            console.log(errorMessage);
                    
                            if(errorMessage != null){
                                this.status = 'error';
                            }
                        }
                    )
                }
            }
        })
    }

    copyMessage(restauranteId: string){
        const selBox = document.createElement('textarea');
        selBox.style.position = 'fixed';
        selBox.style.left = '0';
        selBox.style.top = '0';
        selBox.style.opacity = '0';
        selBox.value = 'https://ralph.tk/restaurant/' + restauranteId;
        // selBox.value = 'https://ralph.com.mx/restaurant/' + restauranteId;
        document.body.appendChild(selBox);
        selBox.focus();
        selBox.select();
        document.execCommand('copy');
        document.body.removeChild(selBox);
    }
}