import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {GLOBAL} from './global';
import {Usuario} from '../models/usuario';

@Injectable()
export class UsuarioService{
    public url:string;
    public identity;
    public identityR;
    public token;

    constructor(public _http: HttpClient){
        this.url = GLOBAL.url;
    }

    register(usuario: Usuario): Observable<any>{
        let params = JSON.stringify(usuario);
        let headers = new HttpHeaders().set('Content-Type', 'application/json');

        return this._http.post(this.url+'registro', params, {headers: headers});
    }

    signup(usuario, gettoken = null): Observable<any>{
        if(gettoken != null){
            usuario.gettoken = gettoken;
        }

        let params = JSON.stringify(usuario);
        let headers = new HttpHeaders().set('Content-Type', 'application/json');

        return this._http.post(this.url+'login', params, {headers: headers});
    }

    getIdentity(){
        let identity = JSON.parse(localStorage.getItem('identity'));

        if(!identity){
            this.identity = null;
        }

        if(identity != "undefined"){
            this.identity = identity;
        } else {
            this.identity = null;
        }

        return this.identity;
    }

    getIdentityR(){
        let identityR = JSON.parse(localStorage.getItem('identityR'));

        if(!identityR){
            this.identityR = null;
        }

        if(identityR != "undefined"){
            this.identityR = identityR;
        } else {
            this.identityR = null;
        }

        return this.identityR;
    }

    getToken(){
        let token = localStorage.getItem('token');

        if(token != "undefined"){
            this.token = token;
        } else {
            this.token = null;
        }

        return this.token;
    }

    actualizarUsuario(usuario: Usuario): Observable<any>{
        let params = JSON.stringify(usuario);
        let headers = new HttpHeaders().set('Content-Type', 'Application/json')
                                        .set('Authorization', this.getToken());
        return this._http.put(this.url+'actualizar-usuario', params, {headers: headers});
    }

    actualizarPermisosUsuario(usuarioId, permiso): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'Application/json')
                                        .set('Authorization', this.getToken());
        return this._http.get(this.url+'actualizar-permisos-usuario/' + usuarioId + '/' + permiso, {headers: headers});
    }

    obtenerUsuario(id):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());

        return this._http.get(this.url+'obtener-usuario/'+id, {headers: headers});
    }

    obtenerUsuario2(id):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());

        return this._http.get(this.url+'obtener-usuario2/'+id, {headers: headers});
    }

    obtenerUsuarios(id):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());

        return this._http.get(this.url+'obtener-usuarios/'+id, {headers: headers});
    }

    codigoVerificacion(telefono):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
        return this._http.get(this.url+'codigo/' + telefono, {headers: headers});
    }

    verificarTelefono(telefono, codigo):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
        return this._http.get(this.url+'verificacion/' + telefono + '/' + codigo, {headers: headers});
    }

    todosLosUsuarios():Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());
        return this._http.get(this.url+'todos-los-usuarios', {headers: headers});
    }

    todosLosUsuarios2(page):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());
        return this._http.get(this.url+'todos-los-usuarios2/' + page, {headers: headers});
    }

    usuariosSinActivar(page):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());
        return this._http.get(this.url+'usuarios-sin-activar/' + page, {headers: headers});
    }

    activarUsuario(id, status):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());
        return this._http.get(this.url+'usuario-activar/' + id + '/' + status, {headers: headers});
    }

    contarUsuarios(status, parametro):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());
        return this._http.get(this.url+'usuario-contar/' + status + '/' + parametro, {headers: headers});
    }

    todosEmpleados():Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());
        return this._http.get(this.url+'todos-los-empleados', {headers: headers});
    }

    eliminarUsuario(id):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());
        return this._http.delete(this.url+'eliminar-usuario/' + id + '/notelasabes', {headers: headers});
    }

    llamarUsuario(usuarioId):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());
        return this._http.get(this.url+'llamada/' + usuarioId, {headers: headers});
    }

    pushNotification(usuarioId):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());
        return this._http.get(this.url+'push-notification/' + usuarioId, {headers: headers});
    }

    prepararContra(usuarioId):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());
        return this._http.get(this.url+'preparar-contra/imyourfather/' + usuarioId, {headers: headers});
    }

    actualizarContra(usuario: Usuario): Observable<any>{
        let params = JSON.stringify(usuario);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());
        return this._http.post(this.url+'actualizar-usuario-contra', params, {headers: headers});
    }

    buscarUsuario(parametro): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', this.getToken());
        return this._http.get(this.url+'buscar-usuario/' + parametro, {headers: headers});
    }
}