import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {PedidoService} from '../../../services/pedido.service';
import {UsuarioService} from '../../../services/usuario.service';
import {RestauranteService} from '../../../services/restaurante.service';
import {Location} from '@angular/common';


@Component({
    selector: 'pedido',
    templateUrl: './pedidoA.component.html',
    providers: [UsuarioService, PedidoService, RestauranteService]
}) 

export class PedidoAComponent implements OnInit{			
    public token;
    public status;
    public pedido;
    public id;
    public usuarios;
    public restaurante;
    public resultado;
    public credito;
    public debe;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _pedidoService: PedidoService,
        private _restauranteService: RestauranteService,
        private _location: Location
    ){
        this.token = this._usuarioService.getToken();
        this.status = null;
    }

    ngOnInit(){
        this.loadPage();
    }

    loadPage(){
        this._route.params.subscribe(params => {
            if(params['id']){ 
                this.id = params['id'];
                this.obtenerPedido(this.id);
            }
        })

        let pedidoProducto = JSON.parse(localStorage.getItem('pedidoProducto'));

        if(pedidoProducto != "undefined" && pedidoProducto != null){
            localStorage.removeItem('pedidoProducto');
        }
    }

    backClicked() {
        // this._router.navigate(['/admin/inicio']);
        this._location.back();
    }

    obtenerPedido(pedidoId){
        this._pedidoService.obtenerPedido(this.token, pedidoId, 1).subscribe(
            response => {
                if(response){
                    this.pedido = response.pedido;
                    console.log(this.pedido);
                    this.obtenerUsuarios(this.pedido.restaurante);
                    this.obtenerRestaurante(this.pedido.restaurante);
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);     
                   
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    actualizarPedido(pedidoId, usuarioId, status){
        this._pedidoService.actualizarPedido(this.token, pedidoId, usuarioId, status).subscribe(
            response => {
                if(response){
                    // this._router.navigate(['/admin/inicio']);
                    this._location.back();
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    eliminarPedido(pedidoId){
        this._pedidoService.eliminarPedido(this.token, pedidoId).subscribe(
            response => {
                if(response){
                    // this._router.navigate(['/admin/inicio']);
                    this._location.back();
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    obtenerUsuarios(restauranteId){
        this._usuarioService.obtenerUsuarios(restauranteId).subscribe(
            response => {
                if(response){
                    this.usuarios = response.usuarios;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    obtenerRestaurante(id){
        this._restauranteService.obtenerRestaurante(id). subscribe(
            response => {
                if(response){
                    this.restaurante = response.restaurante;
                    console.log(this.restaurante);
                    this.credito = response.restaurante.credito;
                    this.debe = response.restaurante.debe;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    actualizarCredito(){
        if(this.pedido.cupon != null){
            var com = (this.pedido.comision*1000) - (this.pedido.cupon*1000) + (this.pedido.envio*1000);
            this.pedido.comision = com / 1000;
        }

        if(this.pedido.comision >= 0){
            if(this.credito > 0){
                var res = (this.credito*1000) - (this.pedido.comision*1000);
                this.resultado = res / 1000;
    
                if(this.resultado > 0){
                    this.credito = this.resultado;
                }
        
                if(this.resultado <= 0){
                    this.credito = 0;
                    this.debe = this.resultado;
                }
            } else if (this.credito <= 0){
                var res2 = (this.debe*1000) - (this.pedido.comision*1000);
                this.resultado = res2 / 1000;
                this.debe = this.resultado;
            }

        } else {
            this.restaurante.ralphDebe = this.restaurante.ralphDebe + (this.pedido.comision * -1);
        }
        
        this.restaurante.credito = this.credito;
        this.restaurante.debe = this.debe;
        

        this._restauranteService.actualizarRestaurante(this.token, this.restaurante).subscribe(
            response => {
                if(response){
                    console.log(response);
                    alert('Crdito: ' + response.restaurante.credito + ', Debe: ' + response.restaurante.debe +', Rdeb: ' + response.restaurante.ralphDebe);
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    whatsAppMandaditos(){
        if(this.restaurante.municipio == '5ea75e0f006fd271928efe33'){
            window.location.href = 'https://api.whatsapp.com/send?phone=523921009557&text=Hola!%20Hay%20un%20pedido%20nuevo%20en%20la%20app';

        } else {
            window.location.href = 'https://api.whatsapp.com/send?phone=523921220020&text=Hola!%20Hay%20un%20pedido%20nuevo%20en%20la%20app';
        }
    }

    whatsApp(tel, n){
        if(n == 1){
            window.location.href = 'https://api.whatsapp.com/send?phone=52' + tel + '&text=%C2%A1Hola!%20Soy%20de%20la%20aplicaci%C3%B3n%20Ralph,%20para%20ordenar%20comida%20a%20domicilio.';

        } else {
            window.location.href = 'https://api.whatsapp.com/send?phone=52' + tel + '&text=Ralph%20-%20Tienes%20un%20pedido%20nuevo%20en%20la%20app';
        }
    }

    copyMessage(tel){
        const selBox = document.createElement('textarea');
        selBox.style.position = 'fixed';
        selBox.style.left = '0';
        selBox.style.top = '0';
        selBox.style.opacity = '0';
        selBox.value = tel;
        document.body.appendChild(selBox);
        selBox.focus();
        selBox.select();
        document.execCommand('copy');
        document.body.removeChild(selBox);
    }

    funcionProducto(producto){
        localStorage.setItem('pedidoProducto', JSON.stringify(producto));
    }
}