import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {GLOBAL} from './global';


@Injectable()
export class StatsService{
    public url: string;

    constructor(private _http: HttpClient){
        this.url = GLOBAL.url;
    }


    nuevaVisitaApp(municipioId, usuarioId, rol): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'stats/' + municipioId + '/' + usuarioId + '/' + rol, {headers: headers});
    }

    nuevaVisita(rol): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'nueva-visita/' + rol, {headers: headers});
    }

    nuevaVisitaRestaurante(restauranteId): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'stat/' + restauranteId, {headers: headers});
    }

    obtenerVisitas(): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'visitas/', {headers: headers});
    }

    nuevavisitaFB(campania, tipoAnuncio, numeroAnuncio, municipio, usuario): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'visitafb/' + campania + '/' + tipoAnuncio + '/' + numeroAnuncio + '/' + municipio + '/' + usuario, {headers: headers});
    }

    nuevaVisitaPublicidad(parametro): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'visitapub/' + parametro, {headers: headers});
    }

    promoCupon(status): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'promo-cupon/' + status, {headers: headers});
    }

    nuevoClickCategoria(id): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'click-categoria/' + id, {headers: headers});
    }

    nuevoPedido(status): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'nuevo-pedido/' + status, {headers: headers});
    }

    cuponExpirado(status): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'cupon-expirado/' + status, {headers: headers});
    }

    allStats(): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'allstats', {headers: headers});
    }

    getStats(collection, page): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'get-stats/' + collection + '/' + page, {headers: headers});
    }
}