import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {ProductoService} from '../../services/producto.service';
import {UsuarioService} from '../../services/usuario.service';
import {RestauranteService} from '../../services/restaurante.service';
import {PedidoService} from '../../services/pedido.service';
import {PedidoProducto} from '../../models/pedidoProducto';
import {CuponService} from '../../services/cupon.service';
import {StatsService} from '../../services/stats.service';
import {Pedido} from '../../models/pedido';
import {Cupon} from '../../models/cupon';
import {GLOBAL} from '../../services/global';
import {Location} from '@angular/common';
import * as $ from 'jquery';


@Component({
    selector: 'pedido',
    templateUrl: './pedido.component.html',
    providers: [ProductoService, RestauranteService, UsuarioService, PedidoService, CuponService, StatsService]
}) 

export class PedidoComponent implements OnInit{
    public restaurante: any;
    public status: string;
    public error: string;
    public identity;
    public token;
    public url: string;
    public productoId: any;
    public pedido: any;
    public pedidoNew: Pedido;
    public comision: number;
    public ganancia: number;
    public total: number;
    public total2: number;
    public pedidoProducto: PedidoProducto;
    public position: any;
    public envio: number;
    public support: boolean;
    public notif: boolean;
    public boton: boolean;
    public cupon: any;
    public cuponCompleto: any;
    public cuponPosicion: any;
    public cupones: any;
    public compraMinima: any;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _productoService: ProductoService,
        private _usuarioService: UsuarioService,
        private _restauranteService: RestauranteService,
        private _pedidoService: PedidoService,
        private _cuponService: CuponService,
        private _statsService: StatsService,
        private _location: Location
    ){
        this.url = GLOBAL.url;
        this.identity = this._usuarioService.getIdentity();
        this.token = this._usuarioService.getToken();
        this.total = null;
        this.status = null;
        this.comision = null;
        this.position = null;
        this.cupon = 0;
        this.cupones = [];
        this.compraMinima = 0;
        this.error = 'Ha ocurrido un error.';
        // this.ganancia = null;
    }

    ngOnInit(){
        this.loadPage();
        this.supportNotification();
        this.obtenerCupones();
        this.checkModal();
    }

    checkModal(){
        if($('.modal.in, .modal.show').length > 0){
            window.location.reload();
        }
    }

    loadPage(){
        this._route.params.subscribe(params => {
            if(params['id']){ 
                this.productoId = params['id'];
                if(this.productoId != 0){
                    this.obtenerPedido(this.productoId);
                } else {
                    this.obtenerPedidoLocal();
                }
            }
        })
    }

    backClicked() {
        this._location.back();
    }

    obtenerPedidoLocal(){
        let pedido = JSON.parse(localStorage.getItem('pedido'));

        if(!pedido){
            this.pedido = null;
            this.status = 'error';
            this._router.navigate(['/']);
        }

        if(pedido != "undefined" && pedido != null){
            this.pedido = pedido;
            this.obtenerRestaurante(this.pedido.restaurante);
            if(this.identity.domicilio){
                this.pedido.direccion = this.identity.domicilio;
            }
        } else {
            this.pedido = null;
            this.status = 'error';
            this._router.navigate(['/']);
        }
    }

    obtenerPedido(id){
        this._pedidoService.obtenerPedido(this.token, id, 2). subscribe(
            response => {
                if(response){
                    this.pedido = response.pedido;

                    if(this.pedido.cupon != null){
                        this.cupon = this.pedido.cupon;
                    }

                    this.restaurante = response.pedido.restaurante;
                    this.envio = this.pedido.envio;
                    this.total = this.pedido.total;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    obtenerRestaurante(id){
        this._restauranteService.obtenerRestaurante(id).subscribe(
            response => {
                if(response.restaurante){
                    this.restaurante = response.restaurante;
                    this.sacarValores();

                    // if(this.restaurante.servicioDomicilio == false){
                    //     this.envio = 20;
                    //     this.sacarValores();
                    // } else {
                    //     this.sacarValores();
                    // }
                }
            },
            error => {
                var errorMessage = <any> error;
                // console.log(errorMessage);

                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    sacarValores(){
        let totalPedido = 0;
        let totalComision = 0;
        let n = 0;
        this.pedido.contenido.forEach(element => {
            totalPedido = element.total + totalPedido;
            totalComision = (element.comision*100) + (totalComision);
            n = n + 1;

            if(this.pedido.contenido.length == n){
                this.total = totalPedido;
                this.comision = totalComision/100;

                if(this.restaurante.envio[0] >= 0 && !this.restaurante.envio[1]){
                    this.envio = this.restaurante.envio[0];
                } else if(this.total >= this.restaurante.envio[1]){
                    this.envio = this.restaurante.envio[2];
                    
                } else if(this.total < this.restaurante.envio[1]){
                    this.envio = this.restaurante.envio[0];
                }
                
                // this.comision = (this.total / 10 ^ 0);
                // this.ganancia = this.total - this.comision;
            }
        })
    }

    posicion(object){
        this.pedidoNew = this.pedido;
        this.position = (this.pedidoNew.contenido.indexOf(object));
    }

    eliminarProducto(){
        this.pedidoNew.contenido.splice(this.position, 1);
        if(this.pedidoNew.contenido.length == 0){
            localStorage.removeItem('pedido');
            localStorage.removeItem('total');
            this._router.navigate(['/restaurant', this.pedido.restaurante]);
        } else {
            let total = JSON.parse(localStorage.getItem('total'));
            this.total2 = total - 1;
            this.pedido.cupon = null;
            this.cupon = 0;
            this.compraMinima = 0;
            localStorage.setItem('pedido', JSON.stringify(this.pedidoNew));
            localStorage.setItem('total', JSON.stringify(this.total2));
            this.obtenerPedidoLocal();
        }
        this.position = null;
    }

    crearPedido(){
        // this.pedido.ganancia = this.ganancia;

        this.pedido.envio = this.envio;
        this.pedido.comision = this.comision;
        this.pedido.total = this.total;

        this.identity.domicilio = this.pedido.direccion;
        localStorage.setItem('identity', JSON.stringify(this.identity));
        
        this._pedidoService.crearPedido(this.token, this.pedido).subscribe(
            response => {
                if(response){

                    if(this.cupon != 0){
                        this.cuponCompleto.cupones.splice(this.cuponPosicion, 1);
                        this.actualizarCupon();
                    }

                    if(this.cupon == 0){
                        var numero = 1;

                    } else if(this.cupon == 20){
                        var numero = 2;

                    } else if(this.cupon == 30){
                        var numero = 3;

                    } else if(this.cupon == 40){
                        var numero = 4;

                    } else if(this.cupon == 50){
                        var numero = 5;
                    }

                    this._statsService.nuevoPedido(numero).subscribe();

                    this._router.navigate(['/gracias', this.restaurante.nombre]);
                    localStorage.removeItem('pedido');
                    localStorage.removeItem('total');
                    this.pedido = null; 
                }
            },
            error => {
                var errorMessage = <any>error;
                if(errorMessage.error.message){
                    this.error = errorMessage.error.message;
                }
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    actualizarPedido(pedidoId, status){
        this._pedidoService.actualizarPedido(this.token, pedidoId, 0, status).subscribe(
            response => {
                if(response){
                    this._router.navigate(['/mis-pedidos']);
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    obtenerCupones(){
        this._cuponService.obtenerCupones(this.token, 1).subscribe(
            response => {
                if(response){                    

                    if(response.cupones.length > 0){
                        response.cupones.forEach(element => {
                            if(element.cupones.length != 0){
                                this.cupones.push(element);
                            }
                        })
                    }
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    usarCupon(cupon, cup){
        this.cupon = cup;
        this.pedido.cupon = this.cupon;

        var n = (this.cupones.indexOf(cupon));
        this.cuponCompleto = this.cupones[n];
        this.cuponPosicion = (this.cupones[n].cupones.indexOf(this.cupon));
        this.compraMinima = this.cuponCompleto.compraMinima;
    }

    actualizarCupon(){
        this._cuponService.actualizarCupon(this.token, this.cuponCompleto).subscribe(
            response => {
                if(response.cupon.cupones.length == 0){
                    if(this.identity.rol != 'ADMIN'){
                        this._statsService.cuponExpirado(0).subscribe();
                    }
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    whatsApp(tel){
        window.location.href = 'https://api.whatsapp.com/send?phone=52' + tel + '&text=%C2%A1Hola!%20Soy%20del%20negocio%20' + this.restaurante.nombre;
    }

    telefono(tel){
        window.location.href = 'tel:' + tel;
    }

    supportNotification(){
        if(!window.Notification){
            this.support = false;
            this.notif = false;
            this.boton = true;
        } else {
            this.support = true;
            if (Notification.permission == 'default'){
                this.notif = false;
                this.boton = false;
            } else {
                this.notif = true;
                this.boton = true;
            }            
        }
    }

    notificationRequest(){
        this.crearPedido();
        Notification.requestPermission( function(permission){
            if (permission == 'granted'){
                console.log('granted');
            }
        }).then(e => {
            this.vapid();
            this.notif = true;
            this.boton = true;
        })
    }

    vapid(){
        if(navigator.serviceWorker){
            const token = this.token;
            navigator.serviceWorker.getRegistration().then(reg => {
            
                this.getPublicKey().then( function(key){       
                    reg.pushManager.subscribe({
                        userVisibleOnly: true,
                        applicationServerKey: key
                    }).then(res => res.toJSON())
                    .then(subscription => {
                        fetch(GLOBAL.url + 'subscribe', {
                            method: 'POST',
                            headers: { 'Content-Type': 'application/json', 'Authorization': token},
                            body: JSON.stringify(subscription)
                        })
                        .catch(console.log);
                    })
                })
            })
        } else {
            // console.log('no lo soporta');
        } 
    }

    getPublicKey(){
        const token = this.token;
        return fetch(this.url + 'key', {
            headers: { 'Authorization': token},
        })
        .then(res => res.arrayBuffer())
        // retornar arreglo, pero como un UnitBarray
        .then(key => new Uint8Array(key));
    }
}