import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {Restaurante} from '../../../restaurantes/models/restaurante';
import {Municipio} from '../../models/municipio';
import {RestauranteService} from '../../../services/restaurante.service';
import {UsuarioService} from '../../../services/usuario.service';
import {MunicipiosService} from '../../../services/municipio.service';
import {Location} from '@angular/common';


@Component({
    selector: 'registrarRest',
    templateUrl: './registrarRest.component.html',
    providers: [RestauranteService, UsuarioService, MunicipiosService]
}) 

export class RegistrarRestComponent implements OnInit{				
    public restaurante: Restaurante;
    public token: any;
    public identity: any;
    public restaurantes: any;
    public restauranteId: any;
    public usuarios: any;
    public municipios: Municipio[];
    public status: string;
    public error: string;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _restauranteService: RestauranteService,
        private _municipiosService: MunicipiosService,
        private _location: Location
    ){
        this.restaurante = new Restaurante("","","","","","","",null,"",null,null,0,null,"",null,null,null);
        this.token = this._usuarioService.getToken();
        this.identity = this._usuarioService.getIdentity();
        this.error = null;
    }

    ngOnInit(){
        this.obtenerMunicipios();
        this.obtenerRestaurantesAdmin();
    }

    obtenerMunicipios(){
        this._municipiosService.obtenerMunicipios().subscribe(
            response => {
                this.municipios = response.municipios;
                this.municipios.splice(2, 1);
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
                this.error = errorMessage.error.message;
            }
        )
    }

    backClicked() {
        // this._router.navigate(['/admin/inicio']);
        this._location.back();
    }

    onSubmit(form){
        // console.log(form._directives);
        this._restauranteService.registrarRestaurante(this.token, this.restaurante).subscribe(
            response => {
                if(response.restaurante && response.restaurante._id){
                    // console.log(response.restaurante);
                    form.reset();
                    this.status = 'success';
                    this.obtenerRestaurantesAdmin();
                } else {
                    // console.log(response);
                    this.status = 'error';
                    this.error = response.message;
                }
            },
            error => {
                var errorMessage = <any>error;
                
                if(errorMessage != null){
                    this.status = 'error';
                    if(errorMessage.error.message){
                        this.error = errorMessage.error.message;
                    } else {
                        this.error = 'Error en el servidor';
                    }
                }
            }
        );
    }

    obtenerRestaurantesAdmin(){
        this._restauranteService.obtenerRestaurantesAdmin(this.token, 0).subscribe(
            response => {
                if(response){
                    this.restaurantes = response.restaurantes
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    obtenerUsuarios(id){
        this.restauranteId = id;
        this._usuarioService.obtenerUsuarios(id).subscribe(
            response => {
                if(response){
                    this.usuarios = response.usuarios;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    actualizarPermisosUsuario(usuarioId, permiso){
        this.status = null;

        this._usuarioService.actualizarPermisosUsuario(usuarioId, permiso).subscribe(
            response => {
                if(response){
                    this.obtenerUsuarios(this.restauranteId);
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error2';
                    this.error = errorMessage.error.message;
                }
            }
        )
    }

    CrarUsuarioConPermisos(form){
        this.status = null;

        this._usuarioService.actualizarPermisosUsuario(this.restauranteId, this.restaurante.visitas).subscribe(
            response => {
                if(response){
                    console.log(response);
                    this.obtenerUsuarios(this.restauranteId);
                    form.reset();
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error2';
                    this.error = errorMessage.error.message;
                }
            }
        )
    }
}