import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {Usuario} from '../../models/usuario';
import {UsuarioService} from '../../services/usuario.service';
import {RestauranteService} from '../../services/restaurante.service';
import * as $ from 'jquery';


@Component({
    selector: 'login',
    templateUrl: './login.component.html',
    providers: [UsuarioService, RestauranteService]
}) 

export class LoginComponent implements OnInit{				
    public title:string;
    public usuario: Usuario;
    public status: string;
    public identity;
    public identityR;
    public token;
    public error: string;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _restauranteService: RestauranteService
    ){
        this.title = 'Identificate';
        this.error = null;
        this.usuario = new Usuario("","","","","","","","","","","","","","","");
    }

    ngOnInit(){
        this.checkModal();
    }

    checkModal(){
        if($('.modal.in, .modal.show').length > 0){
            window.location.reload();
        }
    }

    onSubmit(){
        this._usuarioService.signup(this.usuario).subscribe(
            response => {
                // console.log(response);
                this.identity = response.usuario;
                if(!this.identity || !this.identity._id){
                    this.status = 'error';
                    this.error = response.message;
                } else {
                    localStorage.setItem('identity', JSON.stringify(this.identity));
                        this.getToken();
                }
            },
            error => {
                var errorMessage = <any>error;

                if(errorMessage != null){
                    this.status = 'error';
                    if(errorMessage.error.message){
                        this.error = errorMessage.error.message;
                    } else {
                        this.error = 'Error en el servidor';
                    }
                }
            }
        );
    }

    getToken(){
        this._usuarioService.signup(this.usuario, 'true').subscribe(
            response => {
                this.token = response.token;
                if(this.token.length <= 0){
                    this.status = 'error';
                } else if(this.identity.restaurante && this.identity.restaurante != null){
                    localStorage.setItem('token', this.token);
                    this.getIdentityR();
                } else {
                    localStorage.setItem('token', this.token);
                    this._router.navigate(['/']);
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);

                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        );
    }

    getIdentityR(){
        this._restauranteService.obtenerRestaurante(this.identity.restaurante).subscribe(
            response => {
                this.identityR = response.restaurante;
                if(!this.identityR || !this.identityR._id){
                    this.status = 'error';
                } else {
                    localStorage.setItem('identityR', JSON.stringify(this.identityR));
                        this._router.navigate(['/']);
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);

                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        );
    }
}