import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {Usuario} from '../../models/usuario';
import {Municipio} from '../../admin/models/municipio';
import {UsuarioService} from '../../services/usuario.service';
import {MunicipiosService} from '../../services/municipio.service';
import * as $ from 'jquery';

import { AuthService } from "angularx-social-login";
import { FacebookLoginProvider, GoogleLoginProvider } from "angularx-social-login";
 
 
@Component({
    selector: 'register',
    templateUrl: './register.component.html',
    providers: [UsuarioService, MunicipiosService]
})

export class RegisterComponent implements OnInit{				
    public title:string;
    public usuario: Usuario;
    public status: string;
    public municipios: Municipio[];
    public error: string;
    public municipio: number;
    public numero: number;
    public pedido: any;
    public promocion: any;
    public texto:string;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _municipiosService: MunicipiosService,
        private authService: AuthService
    ){ 
        this.title = 'Registrate';
        this.error = null;
        this.usuario = new Usuario("","","","","","","","","","","","","0","","");
        this.numero = 1;
        this.texto = null;
    }

    ngOnInit(){
        this.loadPage();
        this.obtenerMunicipios();
        this.obtenerMunicipio();
        this.checkModal();
    }

    checkModal(){
        if($('.modal.in, .modal.show').length > 0){
            window.location.reload();
        }
    }

    loadPage(){        
        this.promocion = JSON.parse(localStorage.getItem('promocion'));        
         
        if(this.promocion){
            if(this.promocion[3] == 0){     
                localStorage.setItem('stop', 'true');
            }
            
            if(this.promocion[3] == 0 || this.promocion[3] == 3){
                this.texto = 'Felicidades! Crea una cuenta y obtn tu regalo';
            }
        }
    }

    signInWithFB(): void {
        this.authService.signIn(FacebookLoginProvider.PROVIDER_ID).then((data) => {
            this.usuario.correo = data.email;
            this.usuario.nombre = data.firstName;
            this.usuario.apellido = data.lastName;
            this.usuario.plataforma = 'Facebook';
            this.numero = 2;
        });
    } 
    
    signInWithGoogle(): void {
        this.authService.signIn(GoogleLoginProvider.PROVIDER_ID).then((data) => {
            this.usuario.correo = data.email;
            this.usuario.nombre = data.firstName;
            this.usuario.apellido = data.lastName;
            this.usuario.plataforma = 'Google';
            this.numero = 2;
        });
    }

    signInWithRalph(){
        this.usuario.plataforma = 'Ralph';
        this.numero = 2;
    }


    obtenerMunicipio(){
        let municipio = JSON.parse(localStorage.getItem('municipio'));

        if(!municipio){
            this.municipio = 0;
        }

        if(municipio != "undefined"){
            this.municipio = municipio;
        } else {
            this.municipio = 0;
        }
    }

    obtenerMunicipios(){
        this._municipiosService.obtenerMunicipios().subscribe(
            response => {
                this.municipios = response.municipios;
                this.municipios.splice(2, 1);
            },
            error => {
                var errorMessage = <any>error;
            }
        )
    }

    onSubmit(form){
        if(this.municipio >= 2){
            form.reset();
            this.status = 'error';
            this.error = 'Ya creaste muchas cuentas diferentes';
            return
        }

        this.status = null;
        this.error = null;
        if(form._directives[5].model != form._directives[6].model){
            this.status = 'error';
            this.error = 'Las contraseas no son iguales'
            return
        } else if(this.usuario.telefono.length < 10){
            console.log(this.usuario.telefono);
            this.status = 'error';
            this.error = 'El telfono debe ser de 10 dgitos'
            return
        } else if(this.usuario.password.length < 6){
            console.log(this.usuario.password);
            this.status = 'error';
            this.error = 'La contrasea debe tener al menos 6 dgitos'
            return
        } else {
            this._usuarioService.register(this.usuario).subscribe(
                response => {
                    if(response.usuario && response.usuario._id){
                        this.municipio = this.municipio + 1;
                        localStorage.setItem('municipio', JSON.stringify(this.municipio));

                        form.reset();
                        this._router.navigate(['/verificacion', response.usuario.telefono]);

                        this._usuarioService.codigoVerificacion(response.usuario.telefono).subscribe(
                            response => {
                                if(response){
                                }
                            },
                            error => {
                                var errorMessage = <any>error;
                                console.log(errorMessage);
                        
                                if(errorMessage != null){
                                    this.status = 'error';
                                    this.error = 'Ha ocurrido un error';
                                }
                            }
                        )

                    } else {
                        this.status = 'error';
                        console.log(response);
                        this.error = response.message;
                    }
                },
                error => {
                    this.status = 'error';
                    var errorMessage = <any>error;

                    if(errorMessage != null){                    
                        if(errorMessage.error.message){
                            this.error = errorMessage.error.message;
                        } else {
                            this.error = 'Error en el servidor';
                        }
                    }         
                }
            );
        }
    }
}