import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UsuarioService} from '../../../services/usuario.service';
import {StatsService} from '../../../services/stats.service';
import {Cupon} from '../../../models/cupon';
import {Location} from '@angular/common';

@Component({
    selector: 'usuarios',
    templateUrl: './usuarios.component.html',
    providers: [UsuarioService, StatsService]
}) 

export class UsuariosComponent implements OnInit{				
    public status;
    public loading;
    public usuarios;
    public usuario;
    public page;
    public pages;
    public total;
    public tipoUsuarios;
    public notificaciones;
    public cupones;
    public pedidos;
    public todasLasStats;
    public cupon: Cupon;
    public numero;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _statsService: StatsService,
        private _location: Location
    ){
        this.usuarios = [];
        this.page = 1;
        this.tipoUsuarios = 1;
        this.usuario = null;
        this.numero = null;
        this.notificaciones = [];
        this.cupones = [];
        this.pedidos = [];
        this.loading = true;
        this.todasLasStats = false;
        this.cupon = new Cupon(null,null,"",null,null,null,null)
    }

    ngOnInit(){
        this.obtenerNotificaciones();
        this.obtenerCupones();
        this.obtenerPedidos();
        setTimeout(() => this.obtenerTodosUsuarios(), 300);
    }


    backClicked() {
        this._location.back();
    }
    

    allStats(){
        if(this.todasLasStats == true){
            this.todasLasStats = false;

        } else {
            this.todasLasStats = true;
        }
    }


    onScroll(){
        if(!this.numero){
            this.page += 1;
    
            if(this.page >= this.pages+1){
                return
                
            } else {
                if(this.tipoUsuarios == 1){
                    this.obtenerTodosUsuarios(true);
                    
                } else if(this.tipoUsuarios == 2){
                    this.usuariosActivos(true);
                    
                } else if(this.tipoUsuarios == 3){
                    this.obtenerUsuariosPorActivar(true);
                }
            }
        }
    }


    obtenerTodosUsuarios(adding = false){
        this._statsService.getStats(1, this.page).subscribe(
            response => {
                if(response){
                    this.pages = response.pages;                 
                    this.loading = false;

                    if(!adding){
                        this.usuarios = response.stats;
                        this.total = response.total;
                        
                    } else {
                        var arrayA = this.usuarios;
                        var arrayB = response.stats;
                        this.usuarios = arrayA.concat(arrayB);
                    }


                    response.stats.forEach(usuario => {
                        var pedidos = this.pedidos.filter(x => x == usuario._id).length;
                        var cupones = this.cupones.filter(x => x == usuario._id).length;
                        var notificaciones = this.notificaciones.filter(x => x == usuario._id).length;

                        var posicion = (this.usuarios.indexOf(usuario));
                        this.usuarios[posicion].pedidos = pedidos;
                        this.usuarios[posicion].cupones = cupones;
                        this.usuarios[posicion].notificaciones = notificaciones;
                    });
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    usuariosActivos(adding = false){
        this._usuarioService.todosLosUsuarios2(this.page).subscribe(
            response => {
                if(response){
                    this.pages = response.pages;                
                    this.loading = false;

                    if(!adding){
                        this.usuarios = response.usuarios;
                        this.total = response.total;

                    } else {
                        var arrayA = this.usuarios;
                        var arrayB = response.usuarios;
                        this.usuarios = arrayA.concat(arrayB);
                    }


                    response.usuarios.forEach(usuario => {
                        var pedidos = this.pedidos.filter(x => x == usuario._id).length;
                        var cupones = this.cupones.filter(x => x == usuario._id).length;
                        var notificaciones = this.notificaciones.filter(x => x == usuario._id).length;

                        var posicion = (this.usuarios.indexOf(usuario));
                        this.usuarios[posicion].pedidos = pedidos;
                        this.usuarios[posicion].cupones = cupones;
                        this.usuarios[posicion].notificaciones = notificaciones;
                    });
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    obtenerUsuariosPorActivar(adding = false){
        this._usuarioService.usuariosSinActivar(this.page).subscribe(
            response => {
                if(response){
                    this.pages = response.pages;
                    this.loading = false;

                    if(!adding){
                        this.usuarios = response.usuarios;
                        this.total = response.total;

                    } else {
                        var arrayA = this.usuarios;
                        var arrayB = response.usuarios;
                        this.usuarios = arrayA.concat(arrayB);
                    }


                    response.usuarios.forEach(usuario => {
                        var pedidos = this.pedidos.filter(x => x == usuario._id).length;
                        var cupones = this.cupones.filter(x => x == usuario._id).length;
                        var notificaciones = this.notificaciones.filter(x => x == usuario._id).length;

                        var posicion = (this.usuarios.indexOf(usuario));
                        this.usuarios[posicion].pedidos = pedidos;
                        this.usuarios[posicion].cupones = cupones;
                        this.usuarios[posicion].notificaciones = notificaciones;
                    });
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    obtenerUsuarios(n){
        if(this.tipoUsuarios == n){
            return
        } else {
            this.loading = true;
            this.page = 1;
            this.tipoUsuarios = n;
        }

        if(n == 1){
            this.obtenerTodosUsuarios();
            
        } else if(n == 2){
            this.usuariosActivos();
            
        } else if(n == 3){
            this.obtenerUsuariosPorActivar();
        }
    }


    obtenerNotificaciones(){
        this._statsService.getStats(6, 1).subscribe(
            response => {
                if(response){
                    response.stats.forEach(element => {
                        this.notificaciones.push(element.usuario);
                    });
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    obtenerCupones(){
        this._statsService.getStats(5, 1).subscribe(
            response => {
                if(response){
                    response.stats.forEach(element => {
                        this.cupones.push(element.usuario);
                    });
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    obtenerPedidos(){
        this._statsService.getStats(7, 1).subscribe(
            response => {
                if(response){
                    response.stats.forEach(element => {
                        this.pedidos.push(element.usuario);
                    });
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    cargarUsuario(usuario){
        this.usuario = usuario;
    }


    activarUsuario(n){
        var posicion = (this.usuarios.indexOf(this.usuario));

        this._usuarioService.activarUsuario(this.usuario._id, n).subscribe(
            response => {
                if(response){
                    this.usuario = response.usuario;
                    this.usuarios[posicion].status = this.usuario.status;
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    generarContrasena(){
        this._usuarioService.prepararContra(this.usuario._id).subscribe(
            response => {
                if(response){
                    window.location.href = 'whatsapp://send?phone=52' + this.usuario.telefono + '&text=Tu%20cuenta%20est%C3%A1%20lista%20para%20que%20le%20cambies%20la%20contrase%C3%B1a.%20Para%20continuar,%20accede%20al%20siguiente%20enlace:%20https://ralph.tk/nueva-contra/' + this.usuario._id;
                    // window.location.href = 'whatsapp://send?phone=52' + this.usuario.telefono + '&text=Tu%20cuenta%20est%C3%A1%20lista%20para%20que%20le%20cambies%20la%20contrase%C3%B1a.%20Para%20continuar,%20accede%20al%20siguiente%20enlace:%20https://ralph.com.mx/nueva-contra/' + this.usuario._id;
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    buscarUsuario(){
        if(this.cupon.nombre){
            this._usuarioService.buscarUsuario(this.cupon.nombre). subscribe(
                response => {
                    if(response){
                        this.numero = 1;
                        this.usuarios = response.usuarios;
                    }
                },
                error => {
                    var errorMessage = <any>error;
                    console.log(errorMessage);
            
                    if(errorMessage != null){
                        this.status = 'error';
                    }
                }
            )
        }
    }

    
    whatsApp(){
        if(this.usuario.status == 'por activar'){
            window.location.href = 'https://api.whatsapp.com/send?phone=52' + this.usuario.telefono + '&text=%C2%A1Hola!%20Soy%20de%20la%20aplicaci%C3%B3n%20Ralph,%20para%20ordenar%20comida%20a%20domicilio.%20Intentaste%20crear%20una%20cuenta%20en%20la%20app%20a%20trav%C3%A9s%20de%20este%20n%C3%BAmero%20de%20tel%C3%A9fono,%20pero%20no%20pudiste%20completar%20tu%20registro.%20%C2%BFSurgi%C3%B3%20alg%C3%BAn%20problema?';

        } else {
            window.location.href = 'https://api.whatsapp.com/send?phone=52' + this.usuario.telefono + '&text=%C2%A1Hola!%20Soy%20de%20la%20aplicaci%C3%B3n%20Ralph,%20para%20ordenar%20comida%20a%20domicilio.';
        }
    }


    copyMessage(){
        const selBox = document.createElement('textarea');
        selBox.style.position = 'fixed';
        selBox.style.left = '0';
        selBox.style.top = '0';
        selBox.style.opacity = '0';
        selBox.value = this.usuario.telefono;
        document.body.appendChild(selBox);
        selBox.focus();
        selBox.select();
        document.execCommand('copy');
        document.body.removeChild(selBox);
    }


    eliminarUsuario(){
        this._usuarioService.eliminarUsuario(this.usuario._id).subscribe(
            response => {
                if(response){
                    this.obtenerUsuarios(1);
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }
}