import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {GLOBAL} from './global';
import {Restaurante} from '../restaurantes/models/restaurante';
import {Seccion} from '../restaurantes/models/seccion';
import {Deposito} from '../admin/models/deposito';

@Injectable()
export class RestauranteService{
    public url:string;

    constructor(public _http: HttpClient){
        this.url = GLOBAL.url;
    }


    obtenerRestaurante(restauranteId): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'Application/json');
        return this._http.get(this.url+'restaurante/' + restauranteId, {headers: headers});
    }

    obtenerRestaurantesAdmin(token, status): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'Application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'restaurantes-admin/' + status, {headers: headers});
    }

    actualizarRestaurante(token, restaurante: Restaurante): Observable<any>{
        let params = JSON.stringify(restaurante);
        let headers = new HttpHeaders().set('Content-Type', 'Application/json')
                                        .set('Authorization', token);
        return this._http.put(this.url+'actualizar-restaurante/'+restaurante._id, params, {headers: headers});
    }

    registrarRestaurante(token, restaurante: Restaurante): Observable<any>{
        let params = JSON.stringify(restaurante);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);

        return this._http.post(this.url+'registrar-restaurante', params, {headers: headers});
    }

    obtenerRestaurantes(municipioId, who):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')

        return this._http.get(this.url+'restaurantes/'+ municipioId + '/' + who, {headers: headers});
    }

    obtenerSeccion(seccionId):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')

        return this._http.get(this.url+'obtener-seccion-restaurante/' + seccionId, {headers: headers});
    }
    
    obtenerSecciones(restauranteId, page = 1, who):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')

        return this._http.get(this.url+'obtener-secciones-restaurantes/'+ restauranteId + '/' + who + '/' + page, {headers: headers});
    }

    actualizarSeccion(token, restauranteId, seccionId, seccion: Seccion): Observable<any>{
        let params = JSON.stringify(seccion);
        let headers = new HttpHeaders().set('Content-Type', 'Application/json')
                                        .set('Authorization', token);

        return this._http.put(this.url+'actualizar-seccion-restaurante/'+restauranteId + '/' + seccionId, params, {headers: headers});
    }

    crearSeccion(token, restauranteId, seccion: Seccion): Observable<any>{
        let params = JSON.stringify(seccion);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);

        return this._http.post(this.url+'crear-seccion-restaurante/' + restauranteId, params, {headers: headers});
    }

    eliminarSeccion(token, seccionId): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);

        return this._http.delete(this.url+'eliminar-seccion/' + seccionId, {headers: headers});
    }

    actualizarCredito(token, restauranteId, numero, cantidad): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'credito/' + restauranteId + '/' + numero + '/' + cantidad, {headers: headers});
    }

    darDeAltaRestaurante(token, restauranteId, status): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'alta-restaurante/' + restauranteId + '/' + status, {headers: headers});
    }

    activarRestaurantes(token, horas = 0): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'abrirrestaurantes/estaesmicontrasena/' + horas, {headers: headers});
    }

    obtenerHorario(token, restauranteId): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'obtener-horario/' + restauranteId, {headers: headers});
    }

    opcionesRestaurante(token, restauranteId, opcion): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'opciones-restaurante/' + restauranteId + '/' + opcion, {headers: headers});
    }

    servicioDom(token, restauranteId, opcion): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'servicioDom/' + restauranteId + '/' + opcion, {headers: headers});
    }

    crearDeposito(token, deposito: Deposito): Observable<any>{
        let params = JSON.stringify(deposito);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.post(this.url+'crear-deposito', params, {headers: headers});
    }

    obtenerDeposito(token, depositoId): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'obtener-deposito/' + depositoId, {headers: headers});
    }

    obtenerDepositos(token, restauranteId, page = 1): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'obtener-depositos/' + restauranteId + '/' + page, {headers: headers});
    }

    crearCobro(token, cobro): Observable<any>{
        let params = JSON.stringify(cobro);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.post(this.url+'crear-cobro', params, {headers: headers});
    }

    obtenerCobro(token, cobroId): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'obtener-cobro/' + cobroId, {headers: headers});
    }

    obtenerCobros(token, restauranteId, page = 1): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'obtener-cobros/' + restauranteId + '/' + page, {headers: headers});
    }
}