import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UsuarioService} from '../../../services/usuario.service';
import {PedidoService} from '../../../services/pedido.service';
import {RestauranteService} from '../../../services/restaurante.service';
import {Restaurante} from '../../../restaurantes/models/restaurante';
import {Location} from '@angular/common';
import * as $ from 'jquery';

@Component({
    selector: 'historialRes',
    templateUrl: './historialRes.component.html',
    providers: [UsuarioService, PedidoService, RestauranteService]
}) 

export class HistorialResComponent implements OnInit{				
    public token;
    public status;
    public total;
    public pedidos;
    public page: number;
    public pages: number;
    public loading: boolean;
    public restaurante: Restaurante;
    public depositos;
    public deposit;
    public cobros;
    public tipo;
    public restauranteId;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _restauranteService: RestauranteService,
        private _pedidoService: PedidoService,
        private _location: Location
    ){
        this.token = this._usuarioService.getToken();
        this.pedidos = [];
        this.page = 1;
        this.loading = true;
        this.status = null;
        this.tipo = 1;
    }

    ngOnInit(){
        this.loadPage();
    }


    loadPage(){
        this._route.params.subscribe(params => {
            if(params['id']){ 
                this.restauranteId = params['id'];
                this.obtenerRestaurante();
                this.obtenerPedidos(this.page);
            }
        })
    }


    backClicked() {
        // this._router.navigate(['/restaurante/inicio']);
        this._location.back();
    }


    cambiarTipo(n){
        if(this.tipo != n){
            this.tipo = n;
            this.loading = true;
            this.page = 1;
            
            if(n == 1){
                this.obtenerPedidos(this.page);
                
            } else if(n == 2){
                this.obtenerDepositos(this.page);
                
            } else if(n == 3){
                this.obtenerCobros(this.page);
            }
        }
    }


    onScroll(){
        this.page += 1;

        if(this.page >= this.pages+1){
            return  this.loading = false;
            
        } else {
            if(this.tipo == 1){
                this.obtenerPedidos(this.page, true);
                
            } else if(this.tipo == 2){
                this.obtenerDepositos(this.page, true);
                
            } else if(this.tipo == 3){
                this.obtenerCobros(this.page, true);
            }
            this.loading = true;
        }
    }


    obtenerRestaurante(){
        this._restauranteService.obtenerRestaurante(this.restauranteId).subscribe(
            response => {
                if(response.restaurante){
                    this.restaurante = response.restaurante;
                    localStorage.setItem('identityR', JSON.stringify(this.restaurante));
                }
            },
            error => {
                var errorMessage = <any> error;
                // console.log(errorMessage);
    
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    obtenerPedidos(page, adding = false){
        this._pedidoService.obtenerPedidos(this.token, this.restauranteId, page, 1).subscribe(
            response => {
                if(!response){
                    return this.status = 'error';
                }
                this.pages = response.pages

                if(!adding){
                    this.pedidos = response.pedidos;
                    this.total = response.total;
                    this.loading = false;
                } else {
                    var arrayA = this.pedidos;
                    var arrayB = response.pedidos;
                    this.pedidos = arrayA.concat(arrayB);
                    this.loading = false;
                }            
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    obtenerDepositos(page, adding = false){
        this._restauranteService.obtenerDepositos(this.token, this.restauranteId, page).subscribe(
            response => {
                if(response){
                    this.pages = response.pages
    
                    if(!adding){
                        this.depositos = response.depositos;
                        this.total = response.total;
                        this.loading = false;
                    } else {
                        var arrayA = this.depositos;
                        var arrayB = response.depositos;
                        this.depositos = arrayA.concat(arrayB);
                        this.loading = false;
                    }
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }



    obtenerCobros(page, adding = false){
        this._restauranteService.obtenerCobros(this.token, this.restauranteId, page).subscribe(
            response => {
                if(response){
                    // const sortedActivities = this.cobros.slice().sort((a, b) => b.fecha - a.fecha);
                    this.pages = response.pages
    
                    if(!adding){
                        this.cobros = response.cobros;
                        this.total = response.total;
                        this.loading = false;
                    } else {
                        var arrayA = this.cobros;
                        var arrayB = response.cobros;
                        this.cobros = arrayA.concat(arrayB);
                        this.loading = false;
                    }
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    cargarDeposito(deposit){
        this.deposit = deposit;
    }
}