import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {GLOBAL} from '../../services/global';
import {UsuarioService} from '../../services/usuario.service';
import {PedidoService} from '../../services/pedido.service';
import {RestauranteService} from '../../services/restaurante.service';
import {Location} from '@angular/common';
import * as $ from 'jquery';

@Component({
    selector: 'misPedidos',
    templateUrl: './misPedidos.component.html',
    providers: [UsuarioService, PedidoService, RestauranteService]
}) 

export class MisPedidosComponent implements OnInit{				
    public url: any;
    public token;
    public identity;
    public identityR;
    public status;
    public restaurante: any;
    public pedidos: any;
    public pedidosAnteriores: any;
    public page: number;
    public pages: number;
    public loading: boolean;
    public completedload: boolean;

    constructor(
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _pedidoService: PedidoService,
        private _restauranteService: RestauranteService,
        private _location: Location
    ){
        this.url = GLOBAL.url;
        this.token = this._usuarioService.getToken();
        this.identity = this._usuarioService.getIdentity();
        this.pedidos = [];
        this.pedidosAnteriores = [];
        this.page = 1;
        this.loading = true;
        this.completedload = false;
        this.status = null;
    }

    ngOnInit(){
        this.obtenerPedidos();
        this.obtenerPedidosAnteriores(this.page);
        this.checkModal();
    }

    checkModal(){
        if($('.modal.in, .modal.show').length > 0){
            window.location.reload();
        }
    }

    backClicked() {
        this._location.back();
        // this._router.navigate(['/']);
    }
    
    obtenerPedidos(){
        this._pedidoService.obtenerPedidosUsuario(this.token, this.identity._id, 1, 0).subscribe(
            response => {
                if(!response){
                    this.status = 'error';
                } else {
                    this.pedidos = response.pedidos;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
                
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    onScroll(){
        this.page += 1;
        
        if(this.page == this.pages+1){
            this.loading = false;
        } else {
            this.loading = true;
            this.obtenerPedidosAnteriores(this.page, true);
        }
    }


    obtenerPedidosAnteriores(page, adding = false){
        this._pedidoService.obtenerPedidosUsuario(this.token, this.identity._id, page, 1).subscribe(
            response => {
                if(!response){
                    return this.status = 'error';
                }
                this.completedload = true;
                this.pages = response.pages

                if(!adding){
                    this.pedidosAnteriores = response.pedidos;
                    this.loading = false;
                } else {
                    var arrayA = this.pedidosAnteriores;
                    var arrayB = response.pedidos;
                    this.pedidosAnteriores = arrayA.concat(arrayB);
                    this.loading = false;
                }
            
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }
}