import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {GLOBAL} from './global';
import {Pedido} from '../models/pedido';
import {Notificacion} from '../models/notificacion';

@Injectable()
export class PedidoService{
    public url:string;

    constructor(public _http: HttpClient){
        this.url = GLOBAL.url;
    }

    crearPedido(token, pedido: Pedido): Observable<any>{
        let params = JSON.stringify(pedido);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.post(this.url+'crear-pedido', params, {headers: headers});
    }

    actualizarPedido(token, pedidoId, usuarioId, status): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'Application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'actualizar-pedido/' + pedidoId + '/' + usuarioId + '/' + status, {headers: headers});
    }

    obtenerPedido(token, pedidoId, who): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'Application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'pedido/' + pedidoId + '/' + who, {headers: headers});
    }

    obtenerPedidos(token, restauranteId, page = 1, stats):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'pedidos/'+ restauranteId +'/'+ page +'/'+ stats, {headers: headers});
    }

    obtenerPedidosUsuario(token, usuarioId, page = 1, stats):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'pedidos-usuario/'+ usuarioId +'/'+ page +'/'+ stats, {headers: headers});
    }

    contarPedidos(token, id, who):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'contar-pedidos/'+ id +'/'+ who, {headers: headers});
    }

    eliminarPedido(token, id): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.delete(this.url+'eliminar-pedido/'+ id, {headers: headers});
    }

    push(token, notification: Notificacion){
        let params = JSON.stringify(notification);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.post(this.url+'push', params, {headers: headers});
    }

    obtenerPedidosRepartidor(token):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);
        return this._http.get(this.url+'pedidos-repartidor', {headers: headers});
    }
}