import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UsuarioService} from '../../services/usuario.service';
import {PedidoProducto} from '../../models/pedidoProducto';


@Component({
    selector: 'verificacion',
    templateUrl: './verificacion.component.html',
    providers: [UsuarioService]
}) 

export class VerificacionComponent implements OnInit{				
    public tel: number;
    public codigo: number;
    public status: string;
    public error: string;
    public pedidoProducto: PedidoProducto;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService
    ){
        this.pedidoProducto = new PedidoProducto(null,null,"","",null,"",null, null)
    }

    ngOnInit(){
        this.loadPage();
    }

    loadPage(){
        this._route.params.subscribe(params => {
            if(params['tel']){ 
                this.tel = params['tel'];
            }
        })
    }

    verificarTelefono(form){
        if(this.pedidoProducto.nota.length < 6){
            this.status = 'error';
            this.error = 'El cdigo de verificacin debe ser de 6 dgitos';
            return
        } else {
            this.status = null;
        this._usuarioService.verificarTelefono(this.tel, this.pedidoProducto.nota).subscribe(
            response => {
                if(response.status == "approved"){
                    form.reset();
                    this._router.navigate(['/login']);
                } else {
                    this.status = 'error';
                    this.error = 'El cdigo es incorrecto';
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                    this.error = 'Ha ocurrido un error'
                }
            }
        )
        }
    }
}