import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UsuarioService} from '../../services/usuario.service';
import {PedidoService} from '../../services/pedido.service';
import {RestauranteService} from '../../services/restaurante.service';
import {Location} from '@angular/common';
import {GLOBAL} from '../../services/global';


@Component({
    selector: 'pedidoRe',
    templateUrl: './pedidoRe.component.html',
    providers: [UsuarioService, PedidoService, RestauranteService]
}) 

export class PedidoReComponent implements OnInit{				
    public token;
    public identityR;
    public status;
    public pedido;
    public restaurante;
    public url;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _pedidoService: PedidoService,
        private _restauranteService: RestauranteService,
        private _location: Location
    ){
        this.url = GLOBAL.url;
        this.token = this._usuarioService.getToken();
        this.identityR = this._usuarioService.getIdentityR();
        this.restaurante = this.identityR;
        this.status = null;
    }

    ngOnInit(){
        this.loadPage();
    }

    loadPage(){
        this._route.params.subscribe(params => {
            if(params['id']){ 
                var id = params['id'];
                this.obtenerPedido(id);
            }
        })
    }

    backClicked() {
        this._location.back();
    }


    obtenerPedido(pedidoId){
        this._pedidoService.obtenerPedido(this.token, pedidoId, 3).subscribe(
            response => {
                if(response){
                    this.pedido = response.pedido;
                    this.restaurante = response.pedido.restaurante;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    whatsApp(){
        window.location.href = 'https://api.whatsapp.com/send?phone=52' + this.restaurante.llamadas[0];
    }
}