import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {ProductoService} from '../../../services/producto.service';
import {UsuarioService} from '../../../services/usuario.service';
import {GLOBAL} from '../../../services/global';
import {Location} from '@angular/common';
import * as $ from 'jquery';

@Component({
    selector: 'detalleProd',
    templateUrl: './detalleProd.component.html',
    providers: [UsuarioService, ProductoService]
})

export class DetalleProdComponent implements OnInit{				
    public producto: any;
    public restaurante: any;
    public status: string;
    public identity;
    public token;
    public url: string;
    public productoId: any;
    public pedidoProducto: any;
    public loading: boolean;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _productoService: ProductoService,
        private _location: Location
    ){
        this.token = this._usuarioService.getToken();
        this.identity = this._usuarioService.getIdentity();
        this.restaurante = this._usuarioService.getIdentityR();
        this.url = GLOBAL.url;
        this.status = null;
        this.loading = true;
    }

    ngOnInit(){
        this.loadPage();
        this.checkModal();
    }

    checkModal(){
        if($('.modal.in, .modal.show').length > 0){
            window.location.reload();
        }
    }

    loadPage(){
        this.pedidoProducto = JSON.parse(localStorage.getItem('pedidoProducto'));

        this._route.params.subscribe(params => {
            if(params['id']){ 
                this.productoId = params['id'];
                this.obtenerProducto(this.productoId);
            }
        })
    }

    backClicked() {
        this._location.back();
    }

    obtenerProducto(id){
        this._productoService.obtenerProducto(id).subscribe(
            response => {
                if(response){
                    this.producto = response.producto;
                    this.loading = false;
                    if(this.pedidoProducto.ingredientes && this.pedidoProducto.ingredientes[0]){
                        this.acomodarIngredientes();
                    }
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    acomodarIngredientes(){
        setTimeout(() => {
            this.pedidoProducto.ingredientes.forEach(element => {
                var elemento = [];
                elemento.push(document.getElementById(element));
                elemento[0].checked=true;
            });
        },200)
    }


    checkbox(ing){
        var elemento2 = [];
        elemento2.push(document.getElementById(ing[0]))
        var estado = elemento2[0].checked;

        if(estado == true){
            var elemento = [];
            elemento.push(document.getElementById(ing[0]));
            elemento[0].checked=false;

        } else if(estado == false){
            var elemento = [];
            elemento.push(document.getElementById(ing[0]));
            elemento[0].checked=true;
        }
    }
}