import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {GLOBAL} from '../../../services/global';
import {UsuarioService} from '../../../services/usuario.service';
import {PedidoService} from '../../../services/pedido.service';
import {RestauranteService} from '../../../services/restaurante.service';
import {Pedido} from '../../../models/pedido';
import {Notificacion} from '../../../models/notificacion';
import {Location} from '@angular/common';

@Component({
    selector: 'inicioA',
    templateUrl: './inicioA.component.html',
    providers: [UsuarioService, PedidoService, RestauranteService]
}) 

export class InicioAComponent implements OnInit{				
    public url: any;
    public token;
    public identity;
    public status;
    public total;
    public notificacion: Notificacion;
    public pedidos: Pedido[];
    public usuario: any;
    public page: any;
    public pages: any;
    public statusAdmin: any;

    constructor(
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _pedidoService: PedidoService,
        private _restauranteService: RestauranteService,
        private _location: Location
    ){
        this.url = GLOBAL.url;
        this.token = this._usuarioService.getToken();
        this.identity = this._usuarioService.getIdentity();
        this.usuario = this.identity;
        this.status = null;
        this.pedidos = [];
        this.page = 1;
        this.notificacion = new Notificacion(null,null,"",null,"","");
    }


    ngOnInit(){
        this.obtenerPedidos();
    }
    

    onScroll(){
        this.page += 1;

        if(this.page == this.pages+1){

        } else {
            this.obtenerPedidos(true);
        }
    }


    backClicked() {
        // this._router.navigate(['/']);
        this._location.back();
    }


    obtenerPedidos(adding = false){
        this._pedidoService.obtenerPedidosUsuario(this.token, 0, this.page, 3).subscribe(
            response => {
                if(!response){
                    this.status = 'error';
                } else {
                    this.pages = response.pages;
                    console.log(response);

                    if(!adding){
                        this.pedidos = response.pedidos;
                        this.total = response.total;
                    } else {
                        var arrayA = this.pedidos;
                        var arrayB = response.pedidos;
                        this.pedidos = arrayA.concat(arrayB);
                    }
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    actualizarUsuario(municipioId){
        this.usuario.municipio = municipioId;

        this._usuarioService.actualizarUsuario(this.usuario).subscribe(
            response => {
                if(response.usuario){
                    localStorage.setItem('identity', JSON.stringify(this.identity));
                    // this._router.navigate(['/']);
                    this._location.back();
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }
    

    cargarStatus(statusAdmin){
        this.statusAdmin = statusAdmin;
    }


    actualizarAdmin(){
        this.usuario.imagen = this.statusAdmin;

        this._usuarioService.actualizarUsuario(this.usuario).subscribe(
            response => {
                if(response.usuario){
                    localStorage.setItem('identity', JSON.stringify(this.identity));
                    this.token = this._usuarioService.getToken();
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    // push(title, body, url){
    //     this.notificacion.title = title;
    //     this.notificacion.body = body;
    //     this.notificacion.restaurante = this.identityR._id;
    //     this.notificacion.url = url;

    //     this._pedidoService.push(this.token, this.notificacion).subscribe();
    // }
    

    activarRestaurantes(horas){
        this._restauranteService.activarRestaurantes(this.token, horas).subscribe();
    }


    logout(){
        localStorage.clear();
        this.identity = null;
        this.token = null;
    }
}