    cronometro(){
        this.stopped = false;
        this.promocion = JSON.parse(localStorage.getItem('promocion'));
        this.minuto = this.promocion[0];
        this.segundo = this.promocion[1];
        
        var temporizador = setInterval(() => {
            let stop = JSON.parse(localStorage.getItem('stop'));
            if(stop != null){
                clearInterval(temporizador);
                this.stopped = true;
                this.promocion[0] = 0;
                this.promocion[1] = 0;
                this.promocion[3] = 3;
                this._statsService.promoCupon(3).subscribe();
                localStorage.setItem('promocion', JSON.stringify(this.promocion));
            }

            if(this.minuto == 0 && this.segundo == 0){
                this.stopped = true;
                clearInterval(temporizador);
                this.promocion[3] = 1;
                this._statsService.promoCupon(2).subscribe();
                localStorage.setItem('promocion', JSON.stringify(this.promocion));
            } else {
                this.segundo -= 1;
                this.promocion[0] = this.minuto;
                this.promocion[1] = this.segundo;
    
                localStorage.setItem('promocion', JSON.stringify(this.promocion));
                
                if(this.segundo <= -1){
                    this.minuto -= 1;
                    this.segundo = 59;
                }
            }
        }, 1000)
    }

    obtenerMunicipios(){
        this._municipiosService.obtenerMunicipios().subscribe(
            response => {
                this.municipios = response.municipios;

                if(this.identity == null || this.identity.rol != 'ADMIN'){
                    this.municipios.splice(2, 1);
                }

                if(this.identity == null || this.identity.municipio == '5ea75e24006fd271928efe35'){
                    
                    this.municipios.forEach(mun => {
                        if(mun.nombre == this.mun){
                            this.municipio = mun;
                            this.obtenerRestaurantes(this.municipio._id);
                            this.nuevaVisitaApp(this.municipio._id, 0, 0);
                        }
                    });
                } else {
                    this.mun = this.identity.municipio;

                    this.municipios.forEach(mun => {
                        if(mun._id == this.mun){
                            this.municipio = mun;
                            this.obtenerRestaurantes(this.municipio._id);
                            this.nuevaVisitaApp(this.municipio._id, this.identity._id, this.identity.rol);
                        }
                    });
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
            }
        )
    }

    nuevaVisitaApp(municipioId, usuarioId, rol){
        this.visitas = JSON.parse(localStorage.getItem('visitas'));
        var localTime = moment().unix();

        if(this.visitas){
            // 12 horas = 43200 seg.  1/2 hora = 1800 seg.

            if(localTime - this.visitas[1] >= 43200){
                this._statsService.nuevaVisita(rol).subscribe();
                this.visitas[1] = localTime;
                localStorage.setItem('visitas', JSON.stringify(this.visitas));
            }

            if(localTime - this.visitas[0] >= 1800){
                this._statsService.nuevaVisitaApp(municipioId, usuarioId, rol).subscribe();
                this.visitas[0] = localTime;
                localStorage.setItem('visitas', JSON.stringify(this.visitas));
            }

        } else {
            this._statsService.nuevaVisita(rol).subscribe();
            this._statsService.nuevaVisitaApp(municipioId, usuarioId, rol).subscribe();
            var visit = [];
            visit[1] = localTime;
            visit[0] = localTime;
            localStorage.setItem('visitas', JSON.stringify(visit));
        }

    }

