    contarPedidos(){
        if(this.identity != null){
            this._pedidoService.contarPedidos(this.token, this.identity._id, 1). subscribe(
                response => {
                    if(response){
                        this.pedidosBadge = response.total;
                    }
                },
                error => {
                    var errorMessage = <any>error;
                    // console.log(errorMessage);
            
                    if(errorMessage != null){
                        // this.status = 'error';
                    }
                }
            )

            this._cuponService.obtenerCupones(this.token, 1).subscribe(
                response => {
                    if(response){
                        response.cupones.forEach(element => {
                            this.cuponesBadge = this.cuponesBadge + element.cupones.length;

                            element.cupones.forEach(element2 => {
                                this.cantidad = element2 + this.cantidad;
                            })
                        });
                    }
                },
                error => {
                    var errorMessage = <any>error;
                    console.log(errorMessage);
            
                    if(errorMessage != null){
                        this.status = 'error';
                    }
                }
            )
        }

        if(this.identityR != null){
            this._pedidoService.contarPedidos(this.token, this.identityR._id, 2). subscribe(
                response => {
                    if(response){
                        this.restauranteBadge = response.total;
                    }
                },
                error => {
                    var errorMessage = <any>error;
                    // console.log(errorMessage);
            
                    if(errorMessage != null){
                        // this.status = 'error';
                    }
                }
            )
        }
    }

    onScroll(){
        var n = this.pedirProductos;

        if(this.categoriaActual != 'Categoras'){
            return
        } else {
            // var num = this.restaurantes.length / this.numberOfItems;
            var num = Math.ceil(this.restaurantes.length / this.numberOfItems) - 1;

            if(this.pedirProductos < num){
                this.pedirProductos = this.pedirProductos + 1;

                this.restaurantesArray[n].forEach(element => {
                    this.restaurantes3.push(element);
                    this.obtenerProductosRandom(element._id);
                });
            }
            
            if((n+1) == num){
                n = n + 1;
                this.restaurantesArray[n].forEach(element => {
                    this.restaurantes3.push(element);
                    this.obtenerProductosRandom(element._id);
                });
            }        
        }
    }

    obtenerRestaurantes(municipioId){
        this._restauranteService.obtenerRestaurantes(municipioId, 1).subscribe(
            response => {
                if(response.restaurantes){
                    this.loading = false; 
                    var total = response.restaurantes.length;
                    var elementos = 0;
                    var elementos2 = 0;

                    response.restaurantes.forEach(element => {
                        if(element.status == 'activo'){
                            this.restaurantes.push(element)
                        }
                        elementos = elementos + 1;

                        if(elementos == total){
                            response.restaurantes.forEach(element => {
                                if(element.status == 'inactivo'){
                                    this.restaurantes.push(element)
                                }
                                elementos2 = elementos2 + 1;

                                if(elementos2 == total){
                                    this.restaurantes2 = this.restaurantes;
                                    this.ordenarRestaurantes();
                                }
                            })
                        }
                    })
                }
            },
            error => {
                var errorMessage = <any> error;
                // console.log(errorMessage);
    
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    ordenarRestaurantes(){
        var n = 0;
        var elements = 0;
        var items = this.numberOfItems;

        this.restaurantes.forEach(element => {   
            this.restaurantesArray[n].push(element)
            elements = elements + 1;

            if(elements == items){
                items = items + this.numberOfItems;
                n = n + 1;
            }
        })

        this.onScroll();
    }

