    obtenerProductosRandom(restauranteId){
        this.peticion = restauranteId + '/0/0/0';
        this._productoService.obtenerProductosRandom(this.peticion).subscribe(
            response => {
                if(response.productos){
                    response.productos.forEach(element => {
                        this.productos.push(element);
                    });
                }
            },
            error => {
                var errorMessage = <any> error;
                // console.log(errorMessage);

                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    obtenerProductosCategoria(categoria){
        if(categoria.nombre == this.categoriaActual || this.restaurantes2.length == 0){
            return
        } else {
            this.loading = true;
            this.noDisponibles = false;
            this.categoriaActual = categoria.nombre;
            this.productos = [];
            this.restaurantes3 = [];
            this.restaurantesNew = [];
            var n = 0;
            if(!this.identity && this.identity.rol != 'ADMIN'){
                this._statsService.nuevoClickCategoria(categoria._id).subscribe();
            }

            this.restaurantes2.forEach(element => {
                this.peticion = element._id + '/' + categoria._id + '/0/0';
                this._productoService.obtenerProductosRandom(this.peticion).subscribe(
                    response => {
                        n = n+1;
                        if(response.productos.length){
                            this.restaurantesNew.push(element);
                            response.productos.forEach(element => {
                                this.productos.push(element);                            
                            });
                        }
                        if(this.restaurantes2.length == n){
                            this.loading = false;
                            if(this.restaurantesNew.length == 0){
                                this.noDisponibles = true;
                            }
                            this.restaurantes3 = this.restaurantesNew;
                        }
                    }
                )
            });
        }
    }

