import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {ProductoService} from '../../services/producto.service';
import {UsuarioService} from '../../services/usuario.service';
import {RestauranteService} from '../../services/restaurante.service';
import {StatsService} from '../../services/stats.service';
import {Pedido} from '../../models/pedido';
import {PedidoProducto} from '../../models/pedidoProducto';
import {GLOBAL} from '../../services/global';
import {Location} from '@angular/common';
import * as $ from 'jquery';

@Component({
    selector: 'producto',
    templateUrl: './producto.component.html',
    providers: [ProductoService, RestauranteService, UsuarioService, StatsService]
})

export class ProductoComponent implements OnInit{				
    public producto: any;
    public seccion: any;
    public restaurante: any;
    public status: string;
    public identity;
    public token;
    public url: string;
    public cantidad: number;
    public productoId: any;
    public pedido: Pedido;
    public pedidoProducto: PedidoProducto;
    public total: number;
    public loading: boolean;
    public ingredientes: any;
    public ingredientes2: any;
    public ingredientes3: any;
    public seleccionados: any;
    public ingPrecio: number;
    public ingSelec: any;
    public ingComision: number;
    public ingObli: any;
    public ingObli2: number;
    public ingObli3: boolean;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _productoService: ProductoService,
        private _usuarioService: UsuarioService,
        private _restauranteService: RestauranteService,
        private _statsService: StatsService,
        private _location: Location
    ){
        this.url = GLOBAL.url;
        this.identity = this._usuarioService.getIdentity();
        this.token = this._usuarioService.getToken();
        this.cantidad = 1;
        this.status = null;
        this.loading = true;
        this.pedidoProducto = new PedidoProducto(null,0, "", "", 0, null,null, null)
        this.ingredientes = [];
        this.ingredientes2 = [];
        this.seleccionados = [];
        this.ingPrecio = 0;
        this.ingSelec = [];
        this.ingObli3 = true;
    }

    ngOnInit(){
        this.loadPage();
        this.checkModal();
    }

    checkModal(){
        if($('.modal.in, .modal.show').length > 0){
            window.location.reload();
        }
    }

    loadPage(){
        this._route.params.subscribe(params => {
            if(params['id']){ 
                this.productoId = params['id'];
                this.obtenerProducto(this.productoId);
            }
        })
    }

    obtenerPedidoLocal(){
        let pedido = JSON.parse(localStorage.getItem('pedido'));
        let total = JSON.parse(localStorage.getItem('total'));

        if(!pedido){
            this.pedido = null;
            this.total = null;
        }

        if(pedido != "undefined" && pedido != null){
            this.pedido = pedido;
            this.total = total;
            if(this.pedido.restaurante != this.restaurante._id){
                localStorage.removeItem('pedido');
                localStorage.removeItem('total');
                this.pedido = null;
            }
        } else {
            this.pedido = null;
            this.total = null;
        }
    }

    backClicked() {
        // if(this.pedido != null){
        //     this._router.navigate(['/restaurant', this.producto.restaurante]);
        // }
        this._location.back();
    }

    obtenerProducto(id){
        this._productoService.obtenerProducto(id).subscribe(
            response => {
                if(response){
                    this.producto = response.producto;
                    this.obtenerRestaurante(this.producto.restaurante);
                    this.obtenerSeccion();
                    this.loading = false;

                    if(this.producto.ingredientes[0]){
                        this.acomodarIngredientes();
                    }
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    obtenerRestaurante(id){
        this._restauranteService.obtenerRestaurante(id).subscribe(
            response => {
                if(response.restaurante){
                    this.restaurante = response.restaurante;
                    this.obtenerPedidoLocal();
                }
            },
            error => {
                var errorMessage = <any> error;
                // console.log(errorMessage);
   
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    obtenerSeccion(){
        this._restauranteService.obtenerSeccion(this.producto.seccion).subscribe(
            response => {
                if(response){
                    this.seccion = response.seccion;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    acomodarIngredientes(){
        this.producto.ingredientes.forEach(element => {
            var objeto = [];
            objeto.push(element.minimo);
            objeto.push(element.maximo);
            element.ingredientes.forEach(element => {
                objeto.push(element[0]);             
                this.ingredientes2.push(element);
            });
            this.ingredientes.push(objeto);
        })

        setTimeout(() => {
            var n = 0;
            this.ingredientes.forEach(element => {
                if(element[0] == 1){
                    this.ingredienteUnico(0, n, element[2]);
                }
                n = n+1;
            });
        },200)
    }


    checkbox(ing){
        var n = 0;
        this.ingredientes.forEach(element => {
            if(element.includes(ing[0])){
                if(this.ingredientes[n][0] == 1 && this.ingredientes[n][1] == 1){
                    this.ingredienteUnico(1, n, ing);
                } else {
                    this.ingredienteMultiple(n, ing);
                }
            }
            n = n+1;
        });
    }


    ingredienteUnico(q, n, ing){
        if(q == 0){
            this.seleccionados[n] = [];
            this.seleccionados[n].push(ing);
            var elemento = [];
            elemento.push(document.getElementById(this.seleccionados[n]));
            elemento[0].checked=true;
            this.agregarIngrediente(1, this.seleccionados[n]);

        } else {
            var elemento = [];
            elemento.push(document.getElementById(this.seleccionados[n]));
            elemento[0].checked=false;
            this.agregarIngrediente(2, this.seleccionados[n]);
            this.seleccionados[n] = [];
            this.seleccionados[n].push(ing[0]);
            elemento.push(document.getElementById(this.seleccionados[n]));
            elemento[1].checked=true;
            this.agregarIngrediente(1, ing);
        }  
    } 


    ingredienteMultiple(n, ing){
        var elemento2 = [];
        elemento2.push(document.getElementById(ing[0]))
        var estado = elemento2[0].checked;

        if(estado == true){
            if(this.seleccionados[n] != undefined){
                if(this.seleccionados[n].length < this.ingredientes[n][1]){
                    this.seleccionados[n].push(ing[0]);
                    var elemento = [];
                    elemento.push(document.getElementById(ing[0]));
                    elemento[0].checked=true;
                    this.agregarIngrediente(1, ing);
    
                } else {
                    var elemento = [];
                    elemento.push(document.getElementById(ing[0]));
                    elemento[0].checked=false;
                }
    
            } else {
                this.seleccionados[n] = [];
                this.seleccionados[n].push(ing[0]);
                this.agregarIngrediente(1, ing);
            }

        } else if(estado == false){
            var position = this.seleccionados[n].indexOf(ing[0]);
            this.seleccionados[n].splice(position, 1);
            this.agregarIngrediente(2, ing);
        }
    }


    agregarIngrediente(x, ing){
        if(!ing[1]){
            this.ingredientes2.forEach(element => {
                if(element[0] == ing[0]){
                    this.modificarTotal(element, x);
                }
            });
        } else {
            this.modificarTotal(ing, x);
        }
    }


    modificarTotal(ingrediente, suma){
        if(suma == 1){
            this.ingSelec.push(ingrediente);
            this.ingPrecio = this.ingPrecio + ingrediente[1];
            
        } else if(suma == 2){
            this.ingPrecio = this.ingPrecio - ingrediente[1];
            var position = this.ingSelec.indexOf(ingrediente);
            this.ingSelec.splice(position, 1);
        }
        this.ingObligatorios();
    }


    ingObligatorios(){
        this.ingObli = [];
        this.ingObli2 = 0;
        this.ingObli3 = false;

        this.producto.ingredientes.forEach(element => {
            if(element.minimo > 0){
                var n = 0;
                this.ingObli2 = this.ingObli2 + 1;
                element.ingredientes.forEach(element2 => {
                    if(this.ingSelec.includes(element2) && n == 0){
                        this.ingObli.push(true);
                        n = 1;   
                    }
                });
            }
        });
        if(this.ingObli2 == this.ingObli.length){
            this.ingObli3 = true;
        }
    }
    
    
    crearPedido(n){
        if(this.ingSelec[0]){
            this.ingredientes3 = [];
            this.ingComision = 0;
            this.producto.ingredientes.forEach(element => {
                element.ingredientes.forEach(element2 => {
                    if(this.ingSelec.includes(element2)){
                        this.ingComision = this.ingComision + element2[1];
                        this.ingredientes3.push(element2[0])
                    }
                });
            });
            this.pedidoProducto.ingredientes = this.ingredientes3;
            this.ingComision = (this.ingComision*100*this.cantidad)/1000;
        }

        if(!this.pedidoProducto.nota || this.pedidoProducto.nota.length == 0){
            this.pedidoProducto.nota = null;
        }
        this.pedidoProducto.seccion = this.seccion.nombre;
        this.pedidoProducto.producto = this.producto.nombre;
        this.pedidoProducto.cantidad = this.cantidad;
        this.pedidoProducto._id = this.producto._id;
        this.pedidoProducto.total = (this.producto.precio * this.cantidad);

        
        if(this.producto.precio <= 100){ this.pedidoProducto.comision = (this.producto.precio /10 * this.cantidad);
        } else if(this.producto.precio >= 101 && this.producto.precio <= 115){ this.pedidoProducto.comision = 10 * this.cantidad;
        } else if(this.producto.precio >= 116 && this.producto.precio <= 130){ this.pedidoProducto.comision = 11 * this.cantidad;
        } else if(this.producto.precio >= 131 && this.producto.precio <= 150){ this.pedidoProducto.comision = 12 * this.cantidad;
        } else if(this.producto.precio >= 151 && this.producto.precio <= 175){ this.pedidoProducto.comision = 13 * this.cantidad;
        } else if(this.producto.precio >= 176 && this.producto.precio <= 200){ this.pedidoProducto.comision = 14 * this.cantidad;
        } else if(this.producto.precio >= 201 && this.producto.precio <= 225){ this.pedidoProducto.comision = 15 * this.cantidad;
        } else if(this.producto.precio >= 226 && this.producto.precio <= 250){ this.pedidoProducto.comision = 16 * this.cantidad;
        } else if(this.producto.precio >= 251 && this.producto.precio <= 275){ this.pedidoProducto.comision = 17 * this.cantidad;
        } else if(this.producto.precio >= 276 && this.producto.precio <= 300){ this.pedidoProducto.comision = 18 * this.cantidad;
        } else if(this.producto.precio >= 301 && this.producto.precio <= 333){ this.pedidoProducto.comision = 19 * this.cantidad;
        } else if(this.producto.precio >= 334 && this.producto.precio <= 366){ this.pedidoProducto.comision = 20 * this.cantidad;
        } else if(this.producto.precio >= 367 && this.producto.precio <= 400){ this.pedidoProducto.comision = 21 * this.cantidad;
        } else if(this.producto.precio >= 401 && this.producto.precio <= 433){ this.pedidoProducto.comision = 22 * this.cantidad;
        } else if(this.producto.precio >= 434 && this.producto.precio <= 466){ this.pedidoProducto.comision = 23 * this.cantidad;
        } else if(this.producto.precio >= 467 && this.producto.precio <= 500){ this.pedidoProducto.comision = 24 * this.cantidad;
        } else if(this.producto.precio >= 501 && this.producto.precio <= 533){ this.pedidoProducto.comision = 25 * this.cantidad;
        } else if(this.producto.precio >= 534 && this.producto.precio <= 566){ this.pedidoProducto.comision = 26 * this.cantidad;
        } else if(this.producto.precio >= 567 && this.producto.precio <= 600){ this.pedidoProducto.comision = 27 * this.cantidad;
        } else if(this.producto.precio >= 601 && this.producto.precio <= 633){ this.pedidoProducto.comision = 28 * this.cantidad;
        } else if(this.producto.precio >= 634 && this.producto.precio <= 666){ this.pedidoProducto.comision = 29 * this.cantidad;
        } else if(this.producto.precio >= 667){ this.pedidoProducto.comision = 30 * this.cantidad;
        }

        if(this.ingSelec[0]){
            this.pedidoProducto.comision = (this.pedidoProducto.comision*100 + this.ingComision*100)/100;
            this.pedidoProducto.total = ((this.producto.precio * this.cantidad) + this.ingComision*10);
        }

        if(this.pedido == null){
            this.total = this.cantidad;
            var envio;
            if(this.producto.tiempoEntrega >= 30){
                envio = this.producto.tiempoEntrega
            } else {
                envio = 30
            }
            this.pedido = new Pedido("", this.identity._id, this.restaurante._id, "", "", "", [], envio, "", null, null);
            this.pedido.contenido.push(this.pedidoProducto);
            localStorage.setItem('pedido', JSON.stringify(this.pedido));
            localStorage.setItem('total', JSON.stringify(this.total));
            if(this.identity.rol != 'ADMIN'){
                this._statsService.nuevoPedido(0).subscribe();
            }
            
        } else {
            if(this.pedido.status < this.producto.tiempoEntrega){
                this.pedido.status = this.producto.tiempoEntrega;
            }
            this.total = this.total + this.cantidad;
            this.pedido.contenido.push(this.pedidoProducto);
            localStorage.setItem('pedido', JSON.stringify(this.pedido));
            localStorage.setItem('total', JSON.stringify(this.total));
        }

        if(n == 1){
            this._location.back();

        } else {
            this._location.back();
            setTimeout(() => {
                this._router.navigate(['/pedido/0']);
            }, 50)
            // this._location.replaceState('/pedido/0')
            // window.location.reload()
        }
    }

    irPedido(){
        this._location.back();
        setTimeout(() => {
            this._router.navigate(['/pedido/0']);
        }, 50)
    }

    minus(){
        if(this.cantidad > 1){
            this.cantidad = this.cantidad - 1;
        }
    }

    plus(){
        if(this.cantidad < 10){
            this.cantidad = this.cantidad + 1;
        }
    }
}