import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {GLOBAL} from '../../../services/global';
import {UsuarioService} from '../../../services/usuario.service';
import {StatsService} from '../../../services/stats.service';
import {RestauranteService} from '../../../services/restaurante.service';
import {Location} from '@angular/common';


@Component({
    selector: 'restaurantes',
    templateUrl: './restaurantes.component.html',
    providers: [UsuarioService, StatsService, RestauranteService]
}) 

export class RestaurantesComponent implements OnInit{		
    public url;		
    public token;
    public status;
    public loading;
    public usuarios;
    public usuario;
    public restaurantes;
    public restaurante;
    public total;
    public tipoRestaurantes;
    public notificaciones;
    public pedidos;
    public todasLasStats;
    public mostrarEmpleados;
    public modal;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _statsService: StatsService,
        private _restauranteService: RestauranteService,
        private _location: Location
    ){
        this.token = this._usuarioService.getToken();
        this.url = GLOBAL.url;
        this.tipoRestaurantes = 1;
        this.notificaciones = [];
        this.pedidos = [];
        this.loading = true;
        this.todasLasStats = false;
        this.mostrarEmpleados = false;
    }


    ngOnInit(){
        this.obtenerPedidos();
        this.obtenerNotificaciones();
        setTimeout(() => this.restaurantesActivos(), 200)
    }


    backClicked() {
        this._location.back();
    }


    allStats(){
        if(this.todasLasStats == true){
            this.todasLasStats = false;

        } else {
            this.todasLasStats = true;
        }
    }


    empleados(){
        if(this.mostrarEmpleados == true){
            setTimeout(() => this.mostrarEmpleados = false, 100);

        } else {
            setTimeout(() => this.mostrarEmpleados = true, 100);
            this.todosEmpleados();
        }
    }


    restaurantesActivos(){
        this._restauranteService.obtenerRestaurantesAdmin(this.token, 1).subscribe(
            response => {
                if(response){
                    this.loading = false;
                    this.restaurantes = response.restaurantes;
                    this.total = response.total;

                    this.restaurantes.forEach(restaurante => {
                        var pedidos = this.pedidos.filter(x => x == restaurante._id).length;

                        var posicion = (this.restaurantes.indexOf(restaurante));
                        this.restaurantes[posicion].pedidos = pedidos;
                    });

                    if(this.mostrarEmpleados == true){
                        setTimeout(() => this.todosEmpleados(), 200)
                    }
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    restaurantesPorActivar(){
        this._restauranteService.obtenerRestaurantesAdmin(this.token, 2).subscribe(
            response => {
                if(response){
                    this.loading = false;
                    this.restaurantes = response.restaurantes;
                    this.total = response.total;

                    this.restaurantes.forEach(restaurante => {
                        var pedidos = this.pedidos.filter(x => x == restaurante._id).length;

                        var posicion = (this.restaurantes.indexOf(restaurante));
                        this.restaurantes[posicion].pedidos = pedidos;
                    });

                    if(this.mostrarEmpleados == true){
                        setTimeout(() => this.todosEmpleados(), 200)
                    }
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    todosEmpleados(){
        this._usuarioService.todosEmpleados().subscribe(
            response => {
                if(response){
                    this.usuarios = response.usuarios;

                    this.usuarios.forEach(usuario => {
                        var notificaciones = this.notificaciones.filter(x => x == usuario._id).length;

                        var posicion = (this.usuarios.indexOf(usuario));
                        this.usuarios[posicion].notificaciones = notificaciones;
                    });

                    this.restaurantes.forEach(restaurante => {
                        var posicion = (this.restaurantes.indexOf(restaurante));
                        this.restaurantes[posicion].empleados = [];

                        this.usuarios.forEach(usuario => {
                            if(restaurante._id == usuario.restaurante){
                                if(usuario.correo != 'rafachido12@hotmail.com'){
                                    this.restaurantes[posicion].empleados.push(usuario);
                                }
                            }
                        });
                    });
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    obtenerNotificaciones(){
        this._statsService.getStats(6, 1).subscribe(
            response => {
                if(response){
                    response.stats.forEach(element => {
                        this.notificaciones.push(element.usuario);
                    });
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    obtenerPedidos(){
        this._statsService.getStats(7, 1).subscribe(
            response => {
                if(response){
                    response.stats.forEach(element => {
                        this.pedidos.push(element.restaurante);
                    });
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }
    

    obtenerRestaurantes(n){
        if(this.tipoRestaurantes == n){
            return
        } else {
            this.loading = true;
            this.tipoRestaurantes = n;
        }

        if(n == 1){
            this.restaurantesActivos();
            
        } else if(n == 2){
            this.restaurantesPorActivar();   
        }
    }


    cargarRestaurante(restaurante){
        this.restaurante = restaurante;
    }


    cargarUsuario(usuario){
        this.usuario = usuario;
    }


    elegirModal(n){
        this.modal = n;
    }


    activarRestaurante(status){
        var posicion = (this.restaurantes.indexOf(this.restaurante));

        this._restauranteService.darDeAltaRestaurante(this.token, this.restaurante._id, status).subscribe(
            response => {
                if(response){
                    this.restaurante = response.restaurante;
                    this.restaurantes[posicion].status = this.restaurante.status;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    whatsApp(){
        window.location.href = 'https://api.whatsapp.com/send?phone=52' + this.usuario.telefono + '&text=%C2%A1Hola!%20%C2%BFC%C3%B3mo%20est%C3%A1s?';
    }


    copyMessage(){
        const selBox = document.createElement('textarea');
        selBox.style.position = 'fixed';
        selBox.style.left = '0';
        selBox.style.top = '0';
        selBox.style.opacity = '0';
        selBox.value = this.usuario.telefono;
        document.body.appendChild(selBox);
        selBox.focus();
        selBox.select();
        document.execCommand('copy');
        document.body.removeChild(selBox);
    }


    hacerLlamada(){
        this._usuarioService.llamarUsuario(this.usuario._id).subscribe(
            response => {
                if(response){
                    console.log(response);
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    pushNotification(){
        this._usuarioService.pushNotification(this.usuario._id).subscribe(
            response => {
                if(response){
                    console.log(response);
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }
}