import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {GLOBAL} from './global';
import {Producto} from '../restaurantes/models/producto';

@Injectable()
export class ProductoService{
    public url:string;

    constructor(public _http: HttpClient){
        this.url = GLOBAL.url;
    }

    crearProducto(token, producto: Producto): Observable<any>{
        let params = JSON.stringify(producto);
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);

        return this._http.post(this.url+'crear-producto', params, {headers: headers});
    }

    actualizarproducto(token, restauranteId, producto: Producto): Observable<any>{
        let params = JSON.stringify(producto);
        let headers = new HttpHeaders().set('Content-Type', 'Application/json')
                                        .set('Authorization', token);
        return this._http.put(this.url+'actualizar-producto/'+ restauranteId + '/' + producto._id, params, {headers: headers});
    }

    obtenerProducto(productoId): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'Application/json');
        return this._http.get(this.url+'producto/'+productoId, {headers: headers});
    }
    
    obtenerProductos(peticion, page = 1):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        
        // api.get('/productos/:res?/:cat?/:sec?/:nom?/:page?', ProductoControler.obtenerProductos);

        return this._http.get(this.url+'productos/'+ peticion +'/'+page, {headers: headers});
    }

    obtenerProductosRestaurante(seccionId):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'productosR/'+ seccionId, {headers: headers});
    }
    
    obtenerProductosRandom(peticion):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json');
        return this._http.get(this.url+'productos-random/'+ peticion, {headers: headers});
    }

    activarProducto(token, id, status): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);

        return this._http.get(this.url+'activar-producto/'+ id + '/' + status, {headers: headers});
    }

    eliminarProducto(token, id): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);

        return this._http.delete(this.url+'producto/'+ id, {headers: headers});
    }

    eliminarImagenProducto(token, id): Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json')
                                        .set('Authorization', token);

        return this._http.get(this.url+'imagen-producto/'+ id, {headers: headers});
    }
}