import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {CuponService} from '../../services/cupon.service';
import {UsuarioService} from '../../services/usuario.service';
import {StatsService} from '../../services/stats.service';
import {Cupon} from '../../models/cupon';
import {Location} from '@angular/common';
import * as moment from 'moment';
import * as $ from 'jquery';


@Component({
    selector: 'cupones',
    templateUrl: './cupones.component.html',
    providers: [CuponService, UsuarioService, StatsService]
})

export class CuponesComponent implements OnInit{				
    public identity: any;
    public token: any;
    public cupon: Cupon;
    public cupon2: Cupon;
    public cupones: any;
    public status: string;
    public error: string;
    public loading: boolean;
    public total: number;
    public localTime: any;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _cuponService: CuponService,
        private _statsService: StatsService,
        private _location: Location
    ){
        this.identity = this._usuarioService.getIdentity();
        this.token = this._usuarioService.getToken();
        this.cupon = new Cupon(null,null,"",null,null,null,null)
        this.loading = true;
        this.status = null;
        this.cupones = [];
        this.total = 0;
    }

    ngOnInit(){
        this.obtenerCupones();
        this.checkModal();
    }

    checkModal(){
        if($('.modal.in, .modal.show').length > 0){
            window.location.reload();
        }
    }

    backClicked() {
        // this._router.navigate(['/']);
        this._location.back();
    }

    onScroll(){
        return
    }

    obtenerCupones(){
        this._cuponService.obtenerCupones(this.token, 1).subscribe(
            response => {
                if(response){
                    this.loading = false;
                    this.localTime = moment().unix();

                    if(response.cupones.length > 0){
                        response.cupones.forEach(element => {
                            if(element.cupones.length != 0){
                                this.cupones.push(element);

                                element.cupones.forEach(element2 => {
                                    this.total = element2 + this.total;
                                })
                            }

                            if(element.fechaVencimiento < this.localTime){
                                this.actualizarCupon(element);
                            }
                        })
                    }
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    nuevoCupon(form){
        if(this.cupon.nombre.length == 0){
            return

        } else if(this.cupon.nombre.length < 6){
            this.status = 'error';
            this.error = 'Cdigo invlido';
            form.reset();
            return
        } else {
            this._cuponService.nuevoCupon(this.cupon.nombre, this.token).subscribe(
                response => {
                    if(response){
                        this.error = response.message;
                        form.reset();

                        if(response.message == 'Cupn creado correctamente'){
                            this.status = 'success';
                            this.cupones = [];
                            this.total = 0;
                            this.obtenerCupones();
                        } else {
                            this.status = 'error';
                        }
                    }
                },
                error => {
                    var errorMessage = <any>error;
                    console.log(errorMessage);
            
                    if(errorMessage != null){
                        this.status = 'error';
                        this.error = errorMessage.error.message;
                    }
                }
            )
        }
    }

    actualizarCupon(cupon){
        this.cupon2 = cupon;
        this.cupon2.status = 'Vencido';
        var numero = this.cupon2.cupones.length;

        if(this.identity.rol != 'ADMIN'){
            this._statsService.cuponExpirado(numero).subscribe();
        }
        
        this._cuponService.actualizarCupon(this.token, this.cupon2).subscribe(
            response => {
                if(response){
                    this.obtenerCupones();
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }
}