import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {GLOBAL} from '../../../services/global';
import {UsuarioService} from '../../../services/usuario.service';
import {PedidoService} from '../../../services/pedido.service';
import {RestauranteService} from '../../../services/restaurante.service';
import {Pedido} from '../../../models/pedido';
import {Restaurante} from '../../models/restaurante';
import {Notificacion} from '../../../models/notificacion';
import {Location} from '@angular/common';
import * as $ from 'jquery';


@Component({
    selector: 'inicioR',
    templateUrl: './inicioR.component.html',
    providers: [UsuarioService, PedidoService, RestauranteService]
}) 

export class InicioRComponent implements OnInit{				
    public support: boolean;
    public notif: boolean;
    public permission: any;
    public url: any;
    public token;
    public identity;
    public identityR;
    public status;
    public total;
    public restaurante: Restaurante;
    public notificacion: Notificacion;
    public pedidos: Pedido[];
    public completedload: boolean;
    
    // public innerWidth: any;

    constructor(
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _pedidoService: PedidoService,
        private _restauranteService: RestauranteService,
        private _location: Location
    ){
        this.url = GLOBAL.url;
        this.token = this._usuarioService.getToken();
        this.identity = this._usuarioService.getIdentity();
        this.identityR = this._usuarioService.getIdentityR();
        this.pedidos = [];
        this.notificacion = new Notificacion(null,null,"",null,"","");
        this.completedload = false;
        this.status = null;
    }


    ngOnInit(){
        this.obtenerRestaurante();
        this.supportNotification();
        this.obtenerPedidos();
        this.checkModal();

        // this.innerWidth = window.innerWidth;
        // console.log(this.innerWidth);
    }

    checkModal(){
        if($('.modal.in, .modal.show').length > 0){
            window.location.reload();
        }
    }

    backClicked() {
        // this._router.navigate(['/']);
        this._location.back();
    }

    obtenerRestaurante(){
        this._restauranteService.obtenerRestaurante(this.identityR._id).subscribe(
            response => {
                if(response){
                    this.restaurante = response.restaurante;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    obtenerPedidos(){
        this._pedidoService.obtenerPedidos(this.token, this.identityR._id, 1, 0).subscribe(
            response => {
                if(!response){
                    this.status = 'error';
                } else {
                    this.completedload = true;
                    this.pedidos = response.pedidos;
                    this.total = response.total;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    actualizarPedido(status, pedidoId, usuarioId){
        if(status == 'En preparacion'){
            return
        }

        if(status == 'En espera'){

            this._pedidoService.actualizarPedido(this.token, pedidoId, usuarioId, 1).subscribe(
                response => {
                    if(response){
                        
                    }
                },
                error => {
                    var errorMessage = <any>error;
                    // console.log(errorMessage);
            
                    if(errorMessage != null){
                        this.status = 'error';
                    }
                }
            )
        }
    }

    activarRestaurante(status){
        if(this.restaurante.status == 'por activar'){
            return

        } else if(status == 'activar'){
            this.restaurante.status = 'activo';

        } else if(status == 'desactivar'){
            this.restaurante.status = 'inactivo';
        }

        this._restauranteService.actualizarRestaurante(this.token, this.restaurante).subscribe(
            response => {
                if(response){
                    localStorage.setItem('identityR', JSON.stringify(response.restaurante));
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    supportNotification(){
        if(!window.Notification){
            this.support = false;
            this.notif = false;
        } else {
            this.support = true;
            this.permission = Notification.permission;
            
            if (Notification.permission == 'default'){
                this.notif = false;
            } else {
                this.notif = true;
            }            
        }
    }

    notificationRequest(){
        Notification.requestPermission( function(permission){
            if (permission == 'granted'){
                // console.log('granted');
            }
        }).then(e => {
            this.vapid();
            this.notif = true;
        })
    }

    vapid(){
        if(navigator.serviceWorker){
            const token = this.token;
            navigator.serviceWorker.getRegistration().then(reg => {
            
                this.getPublicKey().then( function(key){                    
                    reg.pushManager.subscribe({
                        userVisibleOnly: true,
                        applicationServerKey: key
                    }).then(res => res.toJSON())
                    .then(subscription => {
                        fetch(GLOBAL.url + 'subscribe', {
                            method: 'POST',
                            headers: { 'Content-Type': 'application/json', 'Authorization': token},
                            body: JSON.stringify(subscription)
                        })
                        .catch(console.log);
                    })
                })
            })
        } else {
            // console.log('no lo soporta');
        } 
    }

    getPublicKey(){
        const token = this.token;
        return fetch(this.url + 'key', {
            headers: { 'Authorization': token},
        })
        .then(res => res.arrayBuffer())
        // retornar arreglo, pero como un UnitBarray
        .then(key => new Uint8Array(key));
    }

    // push(title, body, url){
    //     this.notificacion.title = title;
    //     this.notificacion.body = body;
    //     this.notificacion.restaurante = this.identityR._id;
    //     this.notificacion.url = url;

    //     this._pedidoService.push(this.token, this.notificacion).subscribe();
    // }
    
    copyMessage(restauranteId: string){
        const selBox = document.createElement('textarea');
        selBox.style.position = 'fixed';
        selBox.style.left = '0';
        selBox.style.top = '0';
        selBox.style.opacity = '0';
        selBox.value = 'https://ralph.tk/restaurant/' + restauranteId;
        // selBox.value = 'https://ralph.com.mx/restaurant/' + restauranteId;
        document.body.appendChild(selBox);
        selBox.focus();
        selBox.select();
        document.execCommand('copy');
        document.body.removeChild(selBox);
    }
    
    logout(){
        localStorage.removeItem('pedido');
        localStorage.removeItem('total');
        localStorage.removeItem('identity');
        localStorage.removeItem('identityR');
        localStorage.removeItem('token');
        this.identity = null;
        this.identityR = null;
        this.token = null;
    }
}