import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UsuarioService} from '../../services/usuario.service';
import {CuponService} from '../../services/cupon.service';
import {StatsService} from '../../services/stats.service';


@Component({
    selector: 'redireccionADS',
    templateUrl: './redireccionADS.component.html',
    providers: [UsuarioService, CuponService, StatsService]
}) 

export class RedireccionADSComponent implements OnInit{		
    public identity: any;	
    public token: any;	
    public promocion: any;
    public municipio: any;
    public campania: any;
    public tipoAnuncio: any;
    public numeroAnuncio: any;
    public usuario: any;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _cuponService: CuponService,
        private _statsService: StatsService
    ){
        this.identity = this._usuarioService.getIdentity();
        this.token = this._usuarioService.getToken();
    }


    // this.promocion = [ minutos, segundos, volante o cartel, status, municipio];

    // status =>   0 = persona que se le ha ofrecido apenas el cupn y no tiene una cuenta
    //             1 = persona que ya se le ofreci el cupn Bienvenido o que no lo aprovech la promocin
    //             2 = persona que se le ofreci el cupn Bienvenido o que no lo aprovech y escane el codigo QR
    //             3 = persona que cre su cuenta o que ya la tena creada y se le debe otorgar su cupn
    //             4 = persona que ya se le otorg su cupn



    ngOnInit(){
        this.promo('cartel');
    }

    loadPage(){
        this._route.params.subscribe(params => {

            if(params['campania'] == 'con'){
                this.campania = 'conversiones';

            } else if(params['campania'] == 'alc'){
                this.campania = 'alcance';
            }


            if(params['anuncio'].charAt(0) == 'a'){
                this.tipoAnuncio = 'anuncio';

            } else if(params['anuncio'].charAt(0) == 'h'){
                this.tipoAnuncio = 'historia';
            }
            
            this.numeroAnuncio = params['anuncio'].charAt(1);


            if(params['municipio'] == 'j'){
                this.municipio = 'jamay';

            } else if(params['municipio'] == 'o'){
                this.municipio = 'ocotlan';
            }

            if(this.identity){
                this.usuario = 1;
            } else {
                this.usuario = 0;
            }

            this._statsService.nuevavisitaFB(this.campania, this.tipoAnuncio, this.numeroAnuncio, this.municipio, this.usuario).subscribe();
        });
    }
    

    promo(params){
        this.promocion = JSON.parse(localStorage.getItem('promocion'));
        
        if(!this.promocion){
            this.loadPage();

            if(this.identity == null){
                this.promocion = [15, 0, params, 0, this.municipio];
                localStorage.setItem('promocion', JSON.stringify(this.promocion));
                this._statsService.promoCupon(1).subscribe();
                this._router.navigate(['/']);

            } else {
                this._cuponService.obtenerCupones(this.token, 2).subscribe(
                    response => {
                        if(response.cupones.length == 0){
                            this.promocion = [0, 0, params, 3, this.municipio];
                            localStorage.setItem('promocion', JSON.stringify(this.promocion));
                            this._router.navigate(['/']);

                        } else {
                            this.promocion = [0, 0, 'otro', 2, this.municipio];
                            localStorage.setItem('promocion', JSON.stringify(this.promocion));
                            this._router.navigate(['/']);
                        }
                    },
                    error => {
                        this._router.navigate(['/']);
                        var errorMessage = <any>error;
                        // console.log(errorMessage);
                    }
                )
            }

        } else if(this.promocion[3] && this.promocion[3] == 1){
            this.promocion[3] = 2;
            localStorage.setItem('promocion', JSON.stringify(this.promocion));
            this._router.navigate(['/']);
            
        } else {
            this._router.navigate(['/']);
        }
            
    }
}