import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UsuarioService} from '../../../services/usuario.service';
import {PedidoService} from '../../../services/pedido.service';
import {RestauranteService} from '../../../services/restaurante.service';
import {Pedido} from '../../../models/pedido';
import {Notificacion} from '../../../models/notificacion';
import {Cupon} from '../../../models/cupon';
import {GLOBAL} from '../../../services/global';
import {Location} from '@angular/common';


@Component({
    selector: 'pedidoPrueba',
    templateUrl: './pedidoPrueba.component.html',
    providers: [UsuarioService, PedidoService, RestauranteService]
})

export class PedidoPruebaComponent implements OnInit{
    public status: string;
    public restaurantes: any;
    public restaurante: any;
    public restauranteId: string;
    public credito: number;
    public debe: number;
    public ralphDebe: number;
    public pedido: Pedido;
    public token: any;
    public error: string;
    public notificacion: Notificacion;
    public url: any;
    public cantidad: any;
    public numero: any;
    public cupon: Cupon;

    constructor(
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _pedidoService: PedidoService,
        private _restauranteService: RestauranteService,
        private _location: Location
    ){
        this.url = GLOBAL.url;
        this.token = this._usuarioService.getToken();
        this.restaurantes = [];
        this.restauranteId = null;
        this.credito = null;
        this.error = null;
        this.pedido = new Pedido("","","","","","",[],"","",0,null),
        this.notificacion = new Notificacion(null,null,"",null,"","");
        this.cupon = new Cupon(null,null,"",null,null,null,null);
    }

    ngOnInit(){
        this.obtenerRestaurantesAdmin();
    }

    backClicked() {
        // this._router.navigate(['/admin/inicio']);
        this._location.back();
    }
    
    obtenerRestaurantesAdmin(){
        this._restauranteService.obtenerRestaurantesAdmin(this.token, 0).subscribe(
            response => {
                if(response){
                    this.restaurantes = response.restaurantes;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    obtenerRestaurante(id){
        this._restauranteService.obtenerRestaurante(id). subscribe(
            response => {
                if(response){
                    this.restaurante = response.restaurante;
                    this.restauranteId = response.restaurante._id;
                    this.credito = response.restaurante.credito;
                    this.debe = response.restaurante.debe;
                    this.ralphDebe = response.restaurante.ralphDebe;
                    if(!this.restaurante.envio){
                        this.restaurante.envio = [];
                    }
                    
                    setTimeout(() => {
                        if(this.restaurante.FBads){
                            var elemento = [];
                            elemento.push(document.getElementById('activo'));
                            elemento[0].checked=true;
                        } else {
                            var elemento = [];
                            elemento.push(document.getElementById('inactivo'));
                            elemento[0].checked=true;
                        }

                        if(this.restaurante.servicioDomicilio){
                            var elemento = [];
                            elemento.push(document.getElementById('ellos'));
                            elemento[0].checked=true;
                        } else {
                            var elemento = [];
                            elemento.push(document.getElementById('mandados'));
                            elemento[0].checked=true;
                        }
                    }, 200);
                    
                    if(this.restaurante.pedidoMinimo){
                        this.cupon.cupones = this.restaurante.pedidoMinimo;
                    }

                    console.log(response.restaurante);
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    darDeAltaRestaurante(status){
        this._restauranteService.darDeAltaRestaurante(this.token, this.restauranteId, status).subscribe(
            response => {
                if(response){
                    this.restaurante = response.restaurante;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    cargarEnvio(n){
        this.pedido.cupon = n;
    }

    pedidoPrueba(){
        this.status = null;
        this.pedido.restaurante = this.restauranteId;
        this.pedido.contenido = [ 
            {
                "cantidad" : "1",
                "seccion" : "Desayunos",
                "producto" : "Chilaquiles",
                "total" : 95,
                "nota" : "Sin cebolla",
                "comision" : 9.5,
            }, 
            {
                "cantidad" : "2",
                "seccion" : "Refresco",
                "producto" : "Pepsi",
                "total" : 30,
                "nota" : null,
                "comision" : 3,
            }
        ];
        this.pedido.total = "125";
        this.pedido.comision = "12.5";
        this.pedido.direccion = "Madero #345";
        this.pedido.envio = 20;
        var pedido = this.pedido;

        this._pedidoService.crearPedido(this.token, pedido).subscribe(
            response => {
                if(response){
                    // console.log(response.pedido);
                    this.status = 'pedido-success'                  
                }
            },
            error => {
                var errorMessage = <any>error;
                
                if(errorMessage != null){
                    this.status = 'pedido-error';
                    if(errorMessage.error.message){
                        this.error = errorMessage.error.message;
                    } else {
                        this.error = 'Error en el servidor';
                    }
                }
            }
        )
    }

    actualizarRestaurante(){
        this.obtenerRestaurante(this.restauranteId);
    }

    actualizarCredito(form, numero){
        this.numero = numero;

        if (this.numero == 1){
            this.cantidad = this.cupon.nombre;

        } else if(this.numero == 2){
            this.cantidad = this.cupon.status;

        } else {
            this.cantidad = this.cupon.fechaVencimiento;
        }


        this._restauranteService.actualizarCredito(this.token, this.restauranteId, this.numero, this.cantidad).subscribe(
            response => {
                if(response){
                    this.credito = response.restaurante.credito;
                    this.debe = response.restaurante.debe;
                    this.ralphDebe = response.restaurante.ralphDebe;
                    this.status = 'credito-success'
                    form.reset();
                }
            },
            error => {
                var errorMessage = <any>error;
                
                if(errorMessage != null){
                    this.status = 'credito-error';
                    if(errorMessage.error.message){
                        this.error = errorMessage.error.message;
                    } else {
                        this.error = 'Error en el servidor';
                    }
                }
            }
        )
    }

    opcionesRestaurante(param){
        var opcion;
        if(param == 0){
            if(this.restaurante.pedidoMinimo || this.restaurante.pedidoMinimo == 0){
                opcion = this.restaurante.pedidoMinimo;
            } else { return }

        } else if (param == 1){
            opcion = true;

        } else if (param == 2){
            opcion = false;
        }
        
        this._restauranteService.opcionesRestaurante(this.token, this.restauranteId, opcion).subscribe(
            response => {
                if(response){
                    // console.log(response);
                    if(param == 0){
                        this.status = 'success1'
                    }
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    servicioDom(servicioDomicilio){
        var opcion;

        if (servicioDomicilio == 1){
            opcion = true;

        } else if (servicioDomicilio == 2){
            opcion = false;

        } else if (servicioDomicilio == 0){
            if(this.restaurante.envio[0] || this.restaurante.envio[0] == 0){
                if(this.restaurante.envio.length == 3 && this.restaurante.envio[2] == null){
                    this.restaurante.envio.pop();
                    this.restaurante.envio.pop();
                } else if (this.restaurante.envio.length == 3 && this.restaurante.envio[1] == 0){
                    return alert("No puedes dejar con un $0 la opcin de 'Si el pedido es mayor a'")
                }
                opcion = this.restaurante.envio;
            }
        }

        this._restauranteService.servicioDom(this.token, this.restauranteId, opcion).subscribe(
            response => {
                if(response){
                    // console.log(response);
                    if(servicioDomicilio == 0){
                        if(this.restaurante.envio.length == 1){
                            this.status = 'success2'
                        } else {
                            this.status = 'success3'
                        }
                    }
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    activarRestaurantes(){
        this._restauranteService.activarRestaurantes(this.token).subscribe(
            response => {
                if(response){
                    console.log(response);
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    push(){
        // this.notificacion.title = title;
        // this.notificacion.body = body;
        // this.notificacion.url = url;
        this.notificacion.restaurantes = 'all';
        this.notificacion.title = 'Activa tu restaurante';
        this.notificacion.body = 'No olvides activar tu restaurante, en caso de que vayas a vender. De lo contrario, mantenlo desactivado.';
        this.notificacion.url = '/restaurante/inicio';

        this._pedidoService.push(this.token, this.notificacion).subscribe(
            response => {
                if(response){
                    // console.log(response);
                    this.status = 'notif-success'                  
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'notif-error';
                }
            }
        );
    }

    notificationRequest(){
        Notification.requestPermission( function(permission){
            if (permission == 'granted'){
                // console.log('granted');
            }
        }).then(e => {
            this.vapid();
        })
    }

    vapid(){
        if(navigator.serviceWorker){
            const token = this.token;
            navigator.serviceWorker.getRegistration().then(reg => {
            
                this.getPublicKey().then( function(key){                    
                    reg.pushManager.subscribe({
                        userVisibleOnly: true,
                        applicationServerKey: key
                    }).then(res => res.toJSON())
                    .then(subscription => {
                        fetch(GLOBAL.url + 'subscribe', {
                            method: 'POST',
                            headers: { 'Content-Type': 'application/json', 'Authorization': token},
                            body: JSON.stringify(subscription)
                        })
                        .catch(console.log);
                    })
                })
            })
        } else {
            // console.log('no lo soporta');
        } 
    }

    getPublicKey(){
        const token = this.token;
        return fetch(this.url + 'key', {
            headers: { 'Authorization': token},
        })
        .then(res => res.arrayBuffer())
        // retornar arreglo, pero como un UnitBarray
        .then(key => new Uint8Array(key));
    }
}