    obtenerUsuarios(){
        this._usuarioService.todosLosUsuarios().subscribe(
            response => {
                if(response){
                    
                    response.usuarios.forEach(element => {
                        if(element.municipio == '5ea75e24006fd271928efe35'){
                            var m = (response.usuarios.indexOf(element));
                            response.usuarios.splice(m, 1);
                        }

                        if(element.rol == 'USUARIO'){
                            this.usuarios.push(element);

                        } else {
                            this.restaurantes.push(element);
                        }

                    });

                    setTimeout(() => {
                        this.ordenarUsuarios();
                    }, 500)
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    ordenarUsuarios(){
        this._usuarioService.todosLosUsuarios().subscribe(
            response => {
                if(response){
                    console.log('Todos los usuarios son: ' + this.usuarios);
                    console.log('Todos los restaurantes son: ' + this.restaurantes);

                    var n = 1;
                    
                    this.usuarios.forEach(element => {

                        if(n == 1){
                            this.GrupoAusuarios.push(element)

                        } else if(n == 2){
                            this.GrupoBusuarios.push(element)
                        
                        } else if(n == 3){
                            this.GrupoCusuarios.push(element)
                        
                        } else if(n == 4){
                            this.GrupoDusuarios.push(element)
                        }
                        
                        n = n + 1;

                        if(n == 5){
                            n = 1;
                        }

                    });


                    var x = 1;

                    this.restaurantes.forEach(element => {

                        if(x == 1){
                            this.GrupoArestaurantes.push(element)

                        } else if(x == 2){
                            this.GrupoBrestaurantes.push(element)
                        
                        } else if(x == 3){
                            this.GrupoCrestaurantes.push(element)
                        
                        } else if(x == 4){
                            this.GrupoDrestaurantes.push(element)
                        }
                        
                        x = x + 1;

                        if(x == 5){
                            x = 1;
                        }

                    });

                    console.log('El GrupoAusuarios es -> ' + this.GrupoAusuarios);
                    console.log('El GrupoBusuarios es -> ' + this.GrupoBusuarios);
                    console.log('El GrupoCusuarios es -> ' + this.GrupoCusuarios);
                    console.log('El GrupoDusuarios es -> ' + this.GrupoDusuarios);
                    console.log('El GrupoArestaurantes es -> ' + this.GrupoArestaurantes);
                    console.log('El GrupoBrestaurantes es -> ' + this.GrupoBrestaurantes);
                    console.log('El GrupoCrestaurantes es -> ' + this.GrupoCrestaurantes);
                    console.log('El GrupoDrestaurantes es -> ' + this.GrupoDrestaurantes);
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    
