import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UsuarioService} from '../../services/usuario.service';
import {Usuario} from '../../models/usuario';

@Component({
    selector: 'nuevaContra',
    templateUrl: './nuevaContra.component.html',
    providers: []
}) 

export class NuevaContraComponent implements OnInit{				
    public usuario: Usuario;
    public usuario2: Usuario;
    public status: string;
    public error: string;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService
    ){
        this.usuario2 = new Usuario(null,null,null,null,null,null,null,null,null,null,null,null,null,null,null);
    }

    ngOnInit(){
        this.loadPage();
    }


    loadPage(){
        this._route.params.subscribe(params => {
            if(params['id']){ 
                this.obtenerUsuario(params['id']);
            }
        })
    }


    obtenerUsuario(usuarioId){
        this._usuarioService.obtenerUsuario2(usuarioId).subscribe(
            response => {
                if(response){
                    this.usuario = response.usuario;

                    if(this.usuario.password != 'ListoParaUnaNuevaContra'){
                        delete this.usuario.password;
                        this._router.navigate(['/login']); 
                    }
                }
            },
            error => {
                var errorMessage = <any> error;
                // console.log(errorMessage);

                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    onSubmit(form){
        if(this.usuario.password != 'ListoParaUnaNuevaContra'){
            this.status = 'error';
            this.error = 'No tienes permiso para actualizar la contrasea';
            return

        } else if(this.usuario2.password.length < 6){
            this.status = 'error';
            this.error = 'La contrasea debe tener al menos 6 dgitos';
            return

        } else if(this.usuario2.password != this.usuario2.nombre){
            this.status = 'error';
            this.error = 'Las contraseas no son iguales';
            return
        }

        this.usuario.password = this.usuario2.password;

        this._usuarioService.actualizarContra(this.usuario).subscribe(
            response => {
                if(response){
                    this.status = 'success';
                    form.reset();
                    this._router.navigate(['/login']);
                }
            },
            error =>{
                var errorMessage = <any> error;
                // console.log(errorMessage);
   
                if(errorMessage != null){
                    this.status = 'error';
                    if(errorMessage.error.message){
                        this.error = errorMessage.error.message;
                    } else {
                        this.error = 'Error en el servidor';
                    }
                }
        });
    }  
}