import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UsuarioService} from '../../../services/usuario.service';
import {RestauranteService} from '../../../services/restaurante.service';
import {Deposito} from '../../models/deposito';
import {GLOBAL} from '../../../services/global';
import {Location} from '@angular/common';


@Component({
    selector: 'deposito',
    templateUrl: './deposito.component.html',
    providers: [RestauranteService, UsuarioService]
}) 

export class DepositoComponent implements OnInit{				
    public identity;
    public token;
    public restaurante;
    public status;
    public status2;
    public error;
    public url;
    public deposito;
    public tipo;
    public cobro;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _restauranteService: RestauranteService,
        private _location: Location
    ){
        this.url = GLOBAL.url;
        this.token = this._usuarioService.getToken();
        this.identity = this._usuarioService.getIdentity();
        this.deposito = new Deposito(null,null,null,null,null,[],[],[],null);
        this.error = null;
        this.tipo = 1;
        this.cobro = {};
    }

    ngOnInit(){
        this.loadPage();
    }

    loadPage(){
        this._route.params.subscribe(params => {
            if(params['id']){ 
                this.obtenerRestaurante(params['id']);
            }
        })

        let identityR = JSON.parse(localStorage.getItem('identityR'));

        if(identityR != "undefined" && identityR != null){
            localStorage.removeItem('identityR');
        }
    }


    backClicked() {
        this._location.back();
    }


    cambiarTipo(n){
        this.tipo = n;
    }


    obtenerRestaurante(id){
        this._restauranteService.obtenerRestaurante(id).subscribe(
            response => {
                if(response.restaurante){
                    this.restaurante = response.restaurante;
                    this.deposito.restaurante = this.restaurante._id;
                    console.log(this.restaurante);
                }
            },
            error => {
                var errorMessage = <any> error;
                // console.log(errorMessage);

                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    onSubmit(form){
        this.deposito.credito.push(this.restaurante.credito);
        this.deposito.debe.push(this.restaurante.debe);
        this.deposito.ralphDebe.push(this.restaurante.ralphDebe);

        var ralphDebe = this.restaurante.ralphDebe;
        var debe = this.restaurante.debe*-1;
        var credito = this.restaurante.credito;
        var deposito = this.deposito.cantidad;

        if(ralphDebe > 0){
            debe = ((debe*1000) - (ralphDebe*1000))/1000;

            if(debe >= 0){
                ralphDebe = 0;
                deposito = ((deposito*1000) - (debe*1000))/1000;

                if(deposito >= 0){
                    credito = ((deposito*1000) + (credito*1000))/1000;
                    debe = 0;

                } else {
                    debe = deposito;
                }

            } else {
                ralphDebe = debe*-1;
                debe = 0;
                credito = ((deposito*1000) + (credito*1000))/1000;
            }

        } else {
            deposito = ((deposito*1000) - (debe*1000))/1000;
            if(deposito >= 0){
                debe = 0;
                credito = ((deposito*1000) + (credito*1000))/1000;

            } else {
                debe = deposito;
            }
        }

        this.deposito.ralphDebe.push(ralphDebe);
        this.deposito.debe.push(debe);
        this.deposito.credito.push(credito);


        this._restauranteService.crearDeposito(this.token, this.deposito).subscribe(
            response => {
                if(response){
                    console.log(response);
                    form.reset();
                    this.status2 = 'success';
                    this.restaurante.ralphDebe = ralphDebe;
                    this.restaurante.debe = debe;
                    this.restaurante.credito = credito;
                    this.actualizarCredito();
                    this.deposito.credito = [];
                    this.deposito.debe = [];
                    this.deposito.ralphDebe = [];

                } else {
                    this.status2 = 'error';
                    this.error = response.message;
                }
            },
            error => {
                var errorMessage = <any>error;
                
                if(errorMessage != null){
                    this.status2 = 'error';
                    if(errorMessage.error.message){
                        this.error = errorMessage.error.message;
                    } else {
                        this.error = 'Error en el servidor';
                    }
                }
            }
        )
    }


    onSubmit2(form){
        this.cobro.cantidad = this.deposito._id;
        this.cobro.nota = this.deposito.fecha;
        this.cobro.restaurante = this.restaurante._id;

        var credito = this.restaurante.credito;
        var debe = this.restaurante.debe;

        if(credito > 0){
            var resultado = ((credito*1000) - (this.cobro.cantidad*1000))/1000;

            if(resultado > 0){
                credito = resultado;
            }
    
            if(resultado <= 0){
                credito = 0;
                debe = resultado;
            }

        } else if (credito <= 0){
            var resultado2 = ((debe*1000) - (this.cobro.cantidad*1000))/1000;
            debe = resultado2;
        }


        this._restauranteService.crearCobro(this.token, this.cobro).subscribe(
            response => {
                if(response){
                    console.log(response);
                    form.reset();
                    this.status2 = 'success2';
                    this.restaurante.credito = credito;
                    this.restaurante.debe = debe;
                    this.actualizarCredito();
                    this.deposito.credito = [];
                    this.deposito.debe = [];
                    this.deposito.ralphDebe = [];

                } else {
                    this.status2 = 'error2';
                    this.error = response.message;
                }
            },
            error => {
                var errorMessage = <any>error;
                
                if(errorMessage != null){
                    this.status2 = 'error2';
                    if(errorMessage.error.message){
                        this.error = errorMessage.error.message;
                    } else {
                        this.error = 'Error en el servidor';
                    }
                }
            }
        )
    }


    actualizarCredito(){
        this._restauranteService.actualizarRestaurante(this.token, this.restaurante).subscribe(
            response => {
                if(response){
                    this.restaurante = response.restaurante;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }        
}