import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {RestauranteService} from '../../../services/restaurante.service';
import {UsuarioService} from '../../../services/usuario.service';
import {UploadService} from '../../../services/upload.service';
import {GLOBAL} from '../../../services/global';
import { NgxImageCompressService } from 'ngx-image-compress';
import {Location} from '@angular/common';


@Component({
    selector: 'misDatosR',
    templateUrl: './misDatosR.component.html',
    providers: [RestauranteService, UsuarioService, UploadService]
}) 

export class MisDatosRComponent implements OnInit{				
    public identity;
    public identityR;
    public token;
    public title: string;
    public url: string;
    public restaurante: any;
    public status: string;
    public support;
    public permission;

    public fileToUpload: File;
    public imagesFiles: File;
    public localUrl: any;
    public localCompressedURl:any;
    public imgResultAfterCompress: any;
    public imageFile: any;
    public imageBlob: any;
    public fileName : any;
    public sizeOfOriginalImage:number;
    public sizeOFCompressedImage:number;
    public vecesProceso:number;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _restauranteService: RestauranteService,
        private _uploadService: UploadService,
        private imageCompress: NgxImageCompressService,
        private _location: Location
    ){
        this.title ='Actualizar mis datos';
        this.identity = this._usuarioService.getIdentity();
        this.identityR = this._usuarioService.getIdentityR();
        this.token = this._usuarioService.getToken();
        this.url = GLOBAL.url;
        this.localCompressedURl = null;
        this.status = null;
        this.vecesProceso = 0;
    }

    ngOnInit(){
        this.obtenerRestaurante();
        this.supportNotification();
    }

    backClicked() {
        // this._router.navigate(['/restaurante/inicio']);
        this._location.back();
    }

    obtenerRestaurante(){
        this._restauranteService.obtenerRestaurante(this.identityR._id).subscribe(
            response => {
                if(response){
                    this.restaurante = response.restaurante;
                    localStorage.setItem('identityR', JSON.stringify(this.restaurante));
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    onSubmit(){
        this._restauranteService.actualizarRestaurante(this.token, this.restaurante).subscribe(
            response => {
                if(!response){
                    this.status = 'error';
                } else if(this.fileToUpload && this.fileToUpload.size){
                    this.imageFile = this.convertFile(this.imgResultAfterCompress);
                    this._uploadService.makeFileRequest(this.url+'actualizar-imagen-restaurante/'+this.restaurante._id, this.imageFile, this.token, 'image', 'PUT')
                        .then((result:any) => {
                            this.localCompressedURl = null
                            this.imageFile = [];
                            this.restaurante.imagen = result.restaurante.imagen;
                            this.status = 'success';
                            localStorage.setItem('identityR', JSON.stringify(this.restaurante));
                            this.identityR = this.restaurante;  
                        });
                } else {
                    this.status = 'success';
                    localStorage.setItem('identityR', JSON.stringify(this.restaurante));
                    this.identityR = this.restaurante;               
                }
            },
            error =>{
                var errorMessage = <any> error;
                // console.log(errorMessage);
   
                if(errorMessage != null){
                    this.status = 'error';
                }
        });
    }                    

    selectFile(fileInput: any) {
        this.fileToUpload = fileInput.target.files[0];

        if (this.fileToUpload && this.fileToUpload.size) {
            var reader = new FileReader();
            reader.onload = (fileInput: any) => {
                this.localUrl = fileInput.target.result;
                this.compressFile(this.localUrl)
            }
            reader.readAsDataURL(this.fileToUpload);
        }
    }

    compressFile(image) {
        this.vecesProceso = this.vecesProceso + 1;
        var orientation = -1;

        this.sizeOfOriginalImage = this.imageCompress.byteCount(image)/(1024*1024);
        // console.log('Size in bytes is now:',  this.sizeOfOriginalImage);
        
        var compresion = 80;

        if(this.sizeOfOriginalImage > 50){
            var compresion = 10;

        } else if(this.sizeOfOriginalImage > 10){
            var compresion = 20;

        } else if(this.sizeOfOriginalImage > 1){
            var compresion = 30;

        } else if(this.sizeOfOriginalImage > 0.3){
            var compresion = 40;
        }  

        this.imageCompress.compressFile(image, orientation, compresion, 50).then(
            result => {
                this.imgResultAfterCompress = result;
                this.localCompressedURl = result;

                this.sizeOFCompressedImage = this.imageCompress.byteCount(result)/(1024*1024)
                // console.log('Size in bytes after compression:',  this.sizeOFCompressedImage);

                if(this.sizeOFCompressedImage < 0.4 || this.vecesProceso >= 3){
                    this.vecesProceso = 0;
                    return this.imgResultAfterCompress;
                } else {
                    this.compressFile(this.imgResultAfterCompress)
                }
            });
        }
        
    convertFile(imgResultAfterCompress){
        // call method that creates a blob from dataUri
        const imageBlob = this.dataURItoBlob(imgResultAfterCompress.split(',')[1]);
        
        //imageFile created below is the new compressed file which can be send to API in form data
        const imageFile = new File([imageBlob], 'image.jpeg', { type: 'image/jpeg' });
        return this.imageFile = imageFile;
    }

    dataURItoBlob(dataURI) {
        const byteString = window.atob(dataURI);
        const arrayBuffer = new ArrayBuffer(byteString.length);
        const int8Array = new Uint8Array(arrayBuffer);
        
        for (let i = 0; i < byteString.length; i++) {
            int8Array[i] = byteString.charCodeAt(i);
        }

        const blob = new Blob([int8Array], { type: 'image/jpeg' });
        return blob;
    }

    supportNotification(){
        if(!window.Notification){
            this.support = false;
        } else {
            this.support = true;
            this.permission = Notification.permission;
        }
    }
    
    notificationRequest(){
        Notification.requestPermission( function(permission){
            if (permission == 'granted'){
                // console.log('granted');
            }
        }).then(e => {
            this.vapid();
        })
    }

    vapid(){
        if(navigator.serviceWorker){
            const token = this.token;
            navigator.serviceWorker.getRegistration().then(reg => {
            
                this.getPublicKey().then( function(key){                    
                    reg.pushManager.subscribe({
                        userVisibleOnly: true,
                        applicationServerKey: key
                    }).then(res => res.toJSON())
                    .then(subscription => {
                        fetch(GLOBAL.url + 'subscribe', {
                            method: 'POST',
                            headers: { 'Content-Type': 'application/json', 'Authorization': token},
                            body: JSON.stringify(subscription)
                        })
                        .catch(console.log);
                    })
                })
            })
        } else {
            // console.log('no lo soporta');
        } 
    }

    getPublicKey(){
        const token = this.token;
        return fetch(this.url + 'key', {
            headers: { 'Authorization': token},
        })
        .then(res => res.arrayBuffer())
        // retornar arreglo, pero como un UnitBarray
        .then(key => new Uint8Array(key));
    }
}