import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UsuarioService} from '../../../services/usuario.service';
import {PedidoService} from '../../../services/pedido.service';
import {Pedido} from '../../../models/pedido';
import {Location} from '@angular/common';
import * as $ from 'jquery';


@Component({
    selector: 'ventas',
    templateUrl: './ventas.component.html',
    providers: [UsuarioService, PedidoService]
}) 

export class ventasComponent implements OnInit{
    public token;
    public identityR;
    public status;
    public total;
    public pedidos: Pedido[];
    public page: number;
    public pages: number;
    public loading: boolean;
    public completedload: boolean;
    public restaurante: any;

    constructor(
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _pedidoService: PedidoService,
        private _location: Location
    ){
        this.token = this._usuarioService.getToken();
        this.identityR = this._usuarioService.getIdentityR();
        this.restaurante = this.identityR;
        this.pedidos = [];
        this.page = 1;
        this.loading = false;
        this.completedload = false;
        this.status = null;
    }

    ngOnInit(){
        this.obtenerPedidos(this.page);
        this.checkModal();
    }

    checkModal(){
        if($('.modal.in, .modal.show').length > 0){
            window.location.reload();
        }
    }

    backClicked() {
        // this._router.navigate(['/restaurante/inicio']);
        this._location.back();
    }

    onScroll(){
        this.page += 1;
        
        if(this.page == this.pages+1){
            this.loading = false;
        } else {
            this.loading = true;
            this.obtenerPedidos(this.page, true);
        }
    }


    obtenerPedidos(page, adding = false){
        this._pedidoService.obtenerPedidos(this.token, this.identityR._id, page, 1).subscribe(
            response => {
                if(!response){
                    return this.status = 'error';
                }
                this.completedload = true;
                this.pages = response.pages

                if(!adding){
                    this.pedidos = response.pedidos;
                    this.total = response.total;
                    this.loading = false;
                } else {
                    var arrayA = this.pedidos;
                    var arrayB = response.pedidos;
                    this.pedidos = arrayA.concat(arrayB);
                    this.loading = false;
                }
            
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }
}