import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {PedidoService} from '../../../services/pedido.service';
import {UsuarioService} from '../../../services/usuario.service';
import {RestauranteService} from '../../../services/restaurante.service';
import {Pedido} from '../../../models/pedido';
import {Restaurante} from '../../models/restaurante';
import {Location} from '@angular/common';


@Component({
    selector: 'pedidoR',
    templateUrl: './pedidoR.component.html',
    providers: [UsuarioService, PedidoService, RestauranteService]
}) 

export class PedidoRComponent implements OnInit{				
    public token;
    public identityR;
    public status;
    public pedido;
    public restaurante: Restaurante;
    public id;
    public numero;
    public credito;
    public debe;
    public ralphDebe;
    public resultado;
    public operacion;
    public operacion1;
    public operacion2;
    public operacion3;
    public operacion4;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _pedidoService: PedidoService,
        private _restauranteService: RestauranteService,
        private _location: Location
    ){
        this.token = this._usuarioService.getToken();
        this.identityR = this._usuarioService.getIdentityR();
        this.restaurante = this.identityR;
        this.status = null;
    }

    ngOnInit(){
        this.loadPage();
    }

    loadPage(){
        this._route.params.subscribe(params => {
            if(params['id']){ 
                this.id = params['id'];
                this.obtenerPedido(this.id);
            }

            if(params['numero']){ 
                this.numero = params['numero'];
            }
        })

        let pedidoProducto = JSON.parse(localStorage.getItem('pedidoProducto'));

        if(pedidoProducto != "undefined" && pedidoProducto != null){
            localStorage.removeItem('pedidoProducto');
        }
    }
    

    backClicked() {
        // if(this.numero == 0){
        //     this._router.navigate(['/restaurante/inicio']);
        // }

        // if(this.numero == 1){
        //     this._router.navigate(['/restaurante/ventas']);
        // }

        // if(this.numero == 2){
        //     this._router.navigate(['/restaurante/credito-debo']);
        // }
        this._location.back();
    }

    obtenerPedido(pedidoId){
        this._pedidoService.obtenerPedido(this.token, pedidoId, 1).subscribe(
            response => {
                if(response){
                    this.pedido = response.pedido;

                    var op = (this.pedido.comision*100) - (this.pedido.cupon*100)
                    this.operacion = op / 100;
                    
                    var op1 = (this.pedido.total*100) - (this.pedido.comision*100)
                    this.operacion1 = op1 / 100;
                    
                    var op2 = (this.pedido.total*100) - (this.pedido.comision*100) + (this.pedido.envio*100)
                    this.operacion2 = op2 / 100;
                    
                    var op3 = (this.pedido.total*100) - (this.pedido.cupon*100) + (this.pedido.envio*100)
                    this.operacion3 = op3 / 100;

                    // var op4 = (this.pedido.cupon*100) - (this.pedido.envio*100) - (this.pedido.comision*100)
                    var op4 = (this.pedido.cupon*100) - (this.pedido.comision*100)
                    this.operacion4 = op4 / 100;
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    actualizarPedido(pedidoId, usuarioId, status){
        if(status == 2){
            var direction = ['restaurante/inicio']
        } else {
            var direction = ['restaurante/ventas']
        }

        this._pedidoService.actualizarPedido(this.token, pedidoId, usuarioId, status).subscribe(
            response => {
                if(response){
                    if(status == 2){
                        this.obtenerRestaurante();
                    }

                    // this._router.navigate(direction);
                    this._location.back();
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    obtenerRestaurante(){
        this._restauranteService.obtenerRestaurante(this.identityR._id).subscribe(
            response => {
                if(response){
                    this.restaurante = response.restaurante;
                    this.credito = response.restaurante.credito;
                    this.debe = response.restaurante.debe;
                    this.actualizarCredito();
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    actualizarCredito(){
        if(this.pedido.cupon != null){
            var com = (this.pedido.comision*1000) - (this.pedido.cupon*1000) + (this.pedido.envio*1000);
            this.pedido.comision = com / 1000;
        }

        if(this.pedido.comision >= 0){
            if(this.credito > 0){
                var res = (this.credito*1000) - (this.pedido.comision*1000);
                this.resultado = res / 1000;
    
                if(this.resultado > 0){
                    this.credito = this.resultado;
                }
        
                if(this.resultado <= 0){
                    this.credito = 0;
                    this.debe = this.resultado;
                }
            } else if (this.credito <= 0){
                var res2 = (this.debe*1000) - (this.pedido.comision*1000);
                this.resultado = res2 / 1000;
                this.debe = this.resultado;
            }

        } else {
            this.restaurante.ralphDebe = this.restaurante.ralphDebe + (this.pedido.comision * -1);
        }
        
        this.restaurante.credito = this.credito;
        this.restaurante.debe = this.debe;
        

        this._restauranteService.actualizarRestaurante(this.token, this.restaurante).subscribe(
            response => {
                if(response){
                }
            },
            error => {
                var errorMessage = <any>error;
                // console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }


    whatsAppMandaditos(){
        if(this.restaurante.municipio == '5ea75e0f006fd271928efe33'){
            window.location.href = 'https://api.whatsapp.com/send?phone=523921009557&text=Hola,%20soy%20de%20' + this.restaurante.nombre + ',%20nomas%20para%20avisarte%20que%20el%20pedido%20que%20hubo%20a%20trav%C3%A9s%20de%20la%20aplicaci%C3%B3n%20Ralph,%20ya%20est%C3%A1%20listo%20para%20que%20lo%20recojan.';

        } else {
            window.location.href = 'https://api.whatsapp.com/send?phone=523921220020&text=Hola,%20soy%20de%20' + this.restaurante.nombre + ',%20nomas%20para%20avisarte%20que%20el%20pedido%20que%20hubo%20a%20trav%C3%A9s%20de%20la%20aplicaci%C3%B3n%20Ralph,%20ya%20est%C3%A1%20listo%20para%20que%20lo%20recojan.';
        }
    }


    funcionProducto(producto){
        localStorage.setItem('pedidoProducto', JSON.stringify(producto));
    }


    whatsApp(tel){
        window.location.href = 'https://api.whatsapp.com/send?phone=52' + tel + '&text=%C2%A1Hola!%20Soy%20del%20negocio%20' + this.restaurante.nombre;
    }

    telefono(tel){
        window.location.href = 'tel:' + tel;
    }
}