import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UsuarioService} from '../../services/usuario.service';
import {PedidoService} from '../../services/pedido.service';
import {Location} from '@angular/common';
import {GLOBAL} from '../../services/global';
import * as $ from 'jquery';


@Component({
    selector: 'inicioRe',
    templateUrl: './inicioRe.component.html',
    providers: [UsuarioService, PedidoService]
}) 

export class InicioReComponent implements OnInit{				
    public token;
    public identity;
    public status;
    public pedidos;
    public total;
    public url;
    public notif;
    public support;
    public permission;

    constructor(
        private _router: Router,
        private _usuarioService: UsuarioService,
        private _pedidoService: PedidoService,
        private _location: Location
    ){
        this.url = GLOBAL.url;
        this.token = this._usuarioService.getToken();
        this.identity = this._usuarioService.getIdentity();
        this.pedidos = [];
    }

    ngOnInit(){
        this.obtenerPedidosRepartidor();
        this.supportNotification();
        this.checkModal();
    }

    checkModal(){
        if($('.modal.in, .modal.show').length > 0){
            window.location.reload();
        }
    }

    backClicked() {
        this._location.back();
    }

    obtenerPedidosRepartidor(){
        this._pedidoService.obtenerPedidosRepartidor(this.token).subscribe(
            response => {
                if(response){
                    console.log(response);
                    response.pedidos.forEach(pedido => {
                        if(pedido.restaurante.municipio == this.identity.municipio && pedido.restaurante.servicioDomicilio == false){
                            this.pedidos.push(pedido);
                        }
                    });
                    this.total = this.pedidos.length;
                }
            },
            error => {
                var errorMessage = <any>error;
                console.log(errorMessage);
        
                if(errorMessage != null){
                    this.status = 'error';
                }
            }
        )
    }

    supportNotification(){
        if(!window.Notification){
            this.support = false;
            this.notif = false;
        } else {
            this.support = true;
            this.permission = Notification.permission;
            
            if (Notification.permission == 'default'){
                this.notif = false;
            } else {
                this.notif = true;
            }            
        }
    }

    notificationRequest(){
        Notification.requestPermission( function(permission){
            if (permission == 'granted'){
                // console.log('granted');
            }
        }).then(e => {
            this.vapid();
            this.notif = true;
        })
    }

    vapid(){
        if(navigator.serviceWorker){
            const token = this.token;
            navigator.serviceWorker.getRegistration().then(reg => {
            
                this.getPublicKey().then( function(key){                    
                    reg.pushManager.subscribe({
                        userVisibleOnly: true,
                        applicationServerKey: key
                    }).then(res => res.toJSON())
                    .then(subscription => {
                        fetch(GLOBAL.url + 'subscribe', {
                            method: 'POST',
                            headers: { 'Content-Type': 'application/json', 'Authorization': token},
                            body: JSON.stringify(subscription)
                        })
                        .catch(console.log);
                    })
                })
            })
        } else {
            // console.log('no lo soporta');
        } 
    }

    getPublicKey(){
        const token = this.token;
        return fetch(this.url + 'key', {
            headers: { 'Authorization': token},
        })
        .then(res => res.arrayBuffer())
        // retornar arreglo, pero como un UnitBarray
        .then(key => new Uint8Array(key));
    }
}