import {ModuleWithProviders} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';

// Components
import {LoginComponent} from './components/login/login.component';
import {RegisterComponent} from './components/register/register.component';
import {VerificacionComponent} from './components/verificacion/verificacion.component';
import {InicioComponent} from './components/inicio/inicio.component';
import {MisDatosComponent} from './components/mis-datos/misDatos.component';
import {RestauranteComponent} from './components/restaurante/restaurante.component';
import {ProductoComponent} from './components/producto/producto.component';
import {PedidoComponent} from './components/pedido/pedido.component';
import {MisPedidosComponent} from './components/mis-pedidos/misPedidos.component';
import {GraciasComponent} from './components/gracias/gracias.component';
import {PoliticasComponent} from './components/politicas/politicas.component';
import {NosotrosComponent} from './components/nosotros/nosotros.component';
import {CuponesComponent} from './components/cupones/cupones.component';
import {PremiosComponent} from './components/premios/premios.component';
import {NuevaContraComponent} from './components/nuevaContra/nuevaContra.component';
import {RedireccionQRComponent} from './components/redireccionQR/redireccionQR.component';
import {RedireccionADSComponent} from './components/redireccionADS/redireccionADS.component';
import {RedireccionLinksComponent} from './components/redireccionLinks/redireccionLinks.component';
import {InicioRComponent} from './restaurantes/components/inicioR/inicioR.component';
import {MisDatosRComponent} from './restaurantes/components/misDatosR/misdatosR.component';
import {ProductoRComponent} from './restaurantes/components/productoR/productoR.component';
import {PerfilRestaurantRComponent} from './restaurantes/components/PerfilRestaurantR/PerfilRestaurantR.component';
import {PedidoRComponent} from './restaurantes/components/pedidoR/pedidoR.component';
import {SeccionComponent} from './restaurantes/components/seccion/seccion.component';
import {ventasComponent} from './restaurantes/components/ventas/ventas.component';
import {creditoDeboComponent} from './restaurantes/components/creditoDebo/creditoDebo.component';
import {DepositosComponent} from './restaurantes/components/depositos/depositos.component';
import {DetalleProdComponent} from './restaurantes/components/detalleProd/detalleProd.component';
import {InicioReComponent} from './repartidor/inicioRe/inicioRe.component';
import {PedidoReComponent} from './repartidor/pedidoRe/pedidoRe.component';
import {InicioAComponent} from './admin/components/inicioA/inicioA.component';
import {RegistrarRestComponent} from './admin/components/registrarRest/registrarRest.component';
import {PedidoPruebaComponent} from './admin/components/pedidoPrueba/pedidoPrueba.component';
import {PedidoAComponent} from './admin/components/pedidoA/pedidoA.component';
import {MensajesSMSComponent} from './admin/components/mensajesSMS/mensajesSMS.component';
import {UsuariosComponent} from './admin/components/usuarios/usuarios.component';
import {RestaurantesComponent} from './admin/components/restaurantes/restaurantes.component';
import {EstadisticasComponent} from './admin/components/estadisticas/estadisticas.component';
import {DepositoComponent} from './admin/components/deposito/deposito.component';
import {HistorialResComponent} from './admin/components/historialRes/historialRes.component';

import {UserGuard} from './services/canActivate/user.guard';
import {RepartidorGuard} from './services/canActivate/repartidor.guard';
import {RestaurantGuard} from './services/canActivate/restaurant.guard';
import {AdminGuard} from './services/canActivate/admin.guard';


const appRoutes: Routes = [
    {path: '', component: InicioComponent},
    {path: 'qr/:parametro', component: RedireccionQRComponent},
    {path: 'ads/:campania/:anuncio/:municipio', component: RedireccionADSComponent},
    {path: 'r/:restaurantID', component: RedireccionLinksComponent},
    {path: 'r/:restaurantID/:producto', component: RedireccionLinksComponent},
    {path: 'login', component: LoginComponent},
    {path: 'registro', component: RegisterComponent},
    {path: 'verificacion/:tel', component: VerificacionComponent},
    {path: 'editar-datos/:id', component: MisDatosComponent, canActivate:[AdminGuard]},
    {path: 'producto/:id', component: ProductoComponent, canActivate:[UserGuard]},
    {path: 'mis-pedidos', component: MisPedidosComponent, canActivate:[UserGuard]},
    {path: 'pedido/:id', component: PedidoComponent, canActivate:[UserGuard]},
    {path: 'gracias/:res', component: GraciasComponent, canActivate:[UserGuard]},
    {path: 'cupones', component: CuponesComponent, canActivate:[UserGuard]},
    {path: 'premios', component: PremiosComponent},
    {path: 'nueva-contra/:id', component: NuevaContraComponent},
    {path: 'nosotros', component: NosotrosComponent},
    {path: 'politicas', component: PoliticasComponent},
    {path: 'restaurant/:id', component: RestauranteComponent},
    {path: 'repartidor/inicio', component: InicioReComponent, canActivate:[RepartidorGuard]},
    {path: 'repartidor/pedido/:id', component: PedidoReComponent, canActivate:[RepartidorGuard]},
    {path: 'restaurante/inicio', component: InicioRComponent, canActivate:[RestaurantGuard]},
    {path: 'restaurante/pedido/:id/:numero', component: PedidoRComponent},
    {path: 'restaurante/mis-datos', component: MisDatosRComponent, canActivate:[RestaurantGuard]},
    {path: 'restaurante/perfil', component: PerfilRestaurantRComponent, canActivate:[RestaurantGuard]},
    {path: 'restaurante/producto/:id', component: ProductoRComponent, canActivate:[RestaurantGuard]},
    {path: 'restaurante/seccion/:id', component: SeccionComponent, canActivate:[RestaurantGuard]},
    {path: 'restaurante/ventas', component: ventasComponent, canActivate:[RestaurantGuard]},
    {path: 'restaurante/credito-debo', component: creditoDeboComponent, canActivate:[RestaurantGuard]},
    {path: 'restaurante/depositos', component: DepositosComponent, canActivate:[RestaurantGuard]},
    {path: 'restaurante/detalle-producto/:id', component: DetalleProdComponent},
    {path: 'admin/inicio', component: InicioAComponent, canActivate:[AdminGuard]},
    {path: 'admin/registrar-restaurante', component: RegistrarRestComponent, canActivate:[AdminGuard]},
    {path: 'admin/pedido-prueba', component: PedidoPruebaComponent, canActivate:[AdminGuard]},
    {path: 'admin/pedido/:id', component: PedidoAComponent, canActivate:[AdminGuard]},
    {path: 'admin/mensajessms', component: MensajesSMSComponent, canActivate:[AdminGuard]},
    {path: 'admin/usuarios', component: UsuariosComponent, canActivate:[AdminGuard]},
    {path: 'admin/restaurantes', component: RestaurantesComponent, canActivate:[AdminGuard]},
    {path: 'admin/estadisticas', component: EstadisticasComponent, canActivate:[AdminGuard]},
    {path: 'admin/deposito/:id', component: DepositoComponent, canActivate:[AdminGuard]},
    {path: 'admin/historial/:id', component: HistorialResComponent, canActivate:[AdminGuard]},
    {path: '**', component: InicioComponent}
];

export const appRoutingProviders: any[] = [];
export const routing: ModuleWithProviders = RouterModule.forRoot(appRoutes);